import { makeStyles } from "@material-ui/core";
import colors from "../../theme/statics/colors";
import { TextInputProps } from "./TextInput";

export const useStyles = (props: TextInputProps) => {
  const primayColor =
    props.theme.current.color.textInput.primary ?? colors.primary[100];
  const borderColor = props.theme.current.color.ink[10] ?? colors.ink[10];
  const disabledColor = props.theme.current.color.text[50] ?? colors.text[50];
  const whiteBackground =
    props.theme.current.color.background.white ?? colors.ink[0];
  const bgrDisable =
    props.theme.current.color.textInput.bgrDisable ?? colors.ink[5];
  const disabled = props.disabled;
  const size = props.size ?? "medium";

  return makeStyles((theme) => {
    const root = {
      "& .MuiOutlinedInput-root": {
        borderRadius: 8,
        background: !disabled ? whiteBackground : bgrDisable,
      },
      "& .MuiOutlinedInput-input": {
        textOverflow: "ellipsis",
        fontSize: 14,
        height: size === "medium" ? 24 : 20,
        caretColor: primayColor,
        padding: size === "medium" ? 12 : "10px 12px",
      },
      "& .MuiOutlinedInput-input::placeholder": {
        color: disabledColor,
        opacity: 1,
      },
      "& .MuiOutlinedInput-notchedOutline": {
        borderColor: borderColor,
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        borderWidth: 1,
        borderColor: primayColor,
      },
      "& .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline": {
        borderColor: !disabled ? primayColor : borderColor,
      },
      "& .MuiFormHelperText-contained": {
        marginLeft: 0,
        marginTop: 0,
      },
    };
    const label = {
      maxWidth: "100%",
      display: "flex",
      flexWrap: "nowrap" as "nowrap",
      marginBottom: 4,
    };
    const labelText = {
      marginLeft: 6,
      whiteSpace: "nowrap" as "nowrap",
      overflow: "hidden",
      textOverflow: "ellipsis",
    };
    return {
      root,
      label,
      labelText,
    };
  });
};
