import { makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) => {
  return {
    root: {
      padding: "10px 0",
      borderLeft: "solid 4px",
      borderLeftColor: "transparent",
      borderBottom: `solid 1px ${theme.palette.customColors.ink[10]}`,
      color: theme.palette.customColors.text[100],

      "&:focus, &:focus-within": {
        backgroundColor: theme.palette.customColors.ink[5],
        outline: "none",
        borderLeftColor: theme.palette.customColors.primary[100],
        boxShadow: "0px 2px 8px rgba(0, 0, 0, 0.12)",
      },
    },
    collapsible: {
      display: "flex",
      justifyContent: "center",
    },
    colProduct: {
      minWidth: 0,
      color: theme.palette.customColors.text[100],
    },
    variantImage: {
      width: 46,
      height: 46,
      minWidth: 46,
      borderRadius: 8,
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      background: "#fff",
      border: `solid 1px ${theme.palette.customColors.ink[10]}`,
      "& img": {
        maxWidth: 40,
        maxHeight: 40,
      },
    },
    quantityBtn: {
      padding: 0,
    },
    deleteBtn: {
      padding: 4,
    },
    customNameInput: {
      maxWidth: 500,
      "& input.MuiInputBase-input": {
        fontSize: 16,
      },
    },
    taxInput: {
      fontSize: 14,
      caretColor: theme.palette.customColors.primary[100],
      display: "block",
      borderColor: theme.palette.customColors.ink[10],
      "&:focus-within": {
        borderColor: theme.palette.customColors.primary[100],
      },
    },
    dropdownOption: {
      borderBottom: `solid 1px ${theme.palette.customColors.ink[10]}`,
      paddingTop: 4,
      paddingBottom: 4,
      textAlign: "right",

      "&.not-underlined": {
        borderBottom: "none",
      },
    },
  };
});

export default useStyles;
