import { makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) => {
  return {
    root: {
      width: "calc(100% - 400px)",
      backgroundColor: theme.palette.customColors.background.white,
      position: "relative",
      paddingLeft: 5,
      display: "flex",
      flexDirection: "column",
      borderRight: `solid 12px ${theme.palette.customColors.background.main}`
    },
    lineItemsEmpty: {
      textAlign: "center",
      transform: "translate(0, -50%)",
      position: "absolute",
      top: "50%",
      width: "100%",
    },
    lineItemListHeading: {
      display: "flex",
      justifyContent: "space-between",
      padding: "8px 0",
      fontSize: 14,
      color: theme.palette.customColors.text[70],
      borderBottom: `solid 1px ${theme.palette.customColors.ink[10]}`,
    },
  };
});

export const columnWidths = {
  unit: 90,
  unitPrice: 135,
  quantity: 135,
  toAmount: 120,
  delete: 70,
};

export const columnLeftPaddings = {
  quantity: 40,
  unitPrice: 35,
};
export default useStyles;
