import {
  combineReducers,
  configureStore,
  getDefaultMiddleware,
} from "@reduxjs/toolkit";
import { persistReducer, persistStore } from "redux-persist";
import storage from "redux-persist/lib/storage";
import { useDispatch } from "react-redux";
import tenantContextReducer from "./global-slices/tenant-context/slice";
import tabListReducer from "./pages/create-orders/slices/tabs/slice";
import themeReducer from "./theme/slice";
import orderLineItemsReducer from "./pages/create-orders/slices/order-line-items/slice";
import currentTabReducer from "./pages/create-orders/slices/current-tab/slice";
import customerReducer from "./pages/create-orders/slices/customer/slice";
import hotkeyConfigReducer from "./global-slices/hotkeys/slice";

const rootReducerConfig = {
  key: "sapo-pos-v3",
  storage,
  whitelist: ["tabList", "theme", "orderLineItems", "customer", "currentTab"],
};

const reducers = combineReducers({
  tenantContext: tenantContextReducer,
  tabList: tabListReducer,
  theme: themeReducer,
  orderLineItems: orderLineItemsReducer,
  currentTab: currentTabReducer,
  customer: customerReducer,
  hotkey: hotkeyConfigReducer,
});

const persistedReducer = persistReducer(rootReducerConfig, reducers);

const store = configureStore({
  reducer: persistedReducer,
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: false,
    }),
  devTools: process.env.REACT_APP_ENV === "production" ? false : true,
});
let persistor = persistStore(store);

const storeProvider = { persistor, store };

export type AppState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
export default storeProvider;
export const useAppDispatch = () => useDispatch<AppDispatch>();
