import Dialog from "@material-ui/core/Dialog";
import { Box, Button } from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import IconButton from "@material-ui/core/IconButton";
import React, { Fragment, useState, useEffect, useCallback } from "react";
import { connect, ConnectedProps, useDispatch } from "react-redux";
import PosButton from "../../../../../../components/buttons/PosButton/PosButton";
import {
  ArrowLeftIcon,
  ArrowRightIcon,
  PlusCircleOutlineIcon,
} from "../../../../../../components/SVG";
import { AppState } from "../../../../../../store";
import useStyles from "./TabOrder.style";
import { useResizeDetector } from "react-resize-detector";
import Tab from "./components/Tab/Tab";
import { useWindowSize } from "../../../../../../utils/useWindowSize";
import colors from "../../../../../../theme/statics/colors";
import { updateCurrentTab } from "../../../../slices/current-tab/slice";
import { addTab, removeTab } from "../../../../slices/tabs/slice";

const SEARCH_AND_ACTION_BOX_WIDTH = 924; // chiu rng ca nhng thnh phn khc trn nav_bar, tr list_order.

const TabOrder = (props: PropsFromRedux) => {
  const { tabs, currentTab } = props;
  const [windowWidth] = useWindowSize();
  const [currentWidth, setCurrentWidth] = useState<number>(windowWidth);
  const [trans, setTrans] = useState<number>(0);
  const [showAction, setShowAction] = useState<boolean>(false);
  const [totalWidth, setTotalWidth] = useState<number>(0);

  const classes = useStyles();

  const dispatch = useDispatch<any>();

  useEffect(() => {
    console.log("currentWidth", windowWidth - SEARCH_AND_ACTION_BOX_WIDTH);
    const curWidth = windowWidth - SEARCH_AND_ACTION_BOX_WIDTH ;
    setCurrentWidth(curWidth);
    if (totalWidth - curWidth > 0) {
      console.log("change show action true");
      setShowAction(true);
    } else {
      console.log("change show action false");
      setShowAction(false);
    }
  }, [windowWidth]);

  const handleTransLeft = () => {
    console.log("handleTransLeft", trans);
    if (trans + 196 <= 0) {
      setTrans(trans + 196);
    } else {
      setTrans(0);
    }
  };
  const handleTransRight = () => {
    console.log("handleTransRight", trans);
    if (trans - 196 >= -totalWidth + currentWidth) {
      setTrans(trans - 196);
    } else {
      setTrans(currentWidth - totalWidth);
    }
  };
  const handleCloseDialog = () => {};

  const onResize = useCallback(
    (width: any) => {
      console.log("totalWidth", width);
      console.log("trans", trans);
      setTotalWidth(width);
      const curWidth = windowWidth - SEARCH_AND_ACTION_BOX_WIDTH ;
      console.log(
        "totalWidth - currentWidth ",
        Number(width) - Number(curWidth)
      );
      setTrans(curWidth - width);
      if (width - curWidth > 0) {
        console.log("change show action true");
        setShowAction(true);
      } else {
        console.log("change show action false");
        setTrans(0);
        setShowAction(false);
      }
    },
    [windowWidth]
  );
  const { ref } = useResizeDetector({ onResize });
  // change state to props from store

  return (
    <Fragment>
      <Dialog
        open={false}
        onClose={handleCloseDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Use Google's location service?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Let Google help apps determine location. This means sending
            anonymous location data to Google, even when no apps are running.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <PosButton onClick={handleCloseDialog}>Disagree</PosButton>
          <PosButton onClick={handleCloseDialog} autoFocus>
            Agree
          </PosButton>
        </DialogActions>
      </Dialog>
      <Box className={classes.listWrap}>
        {showAction && (
          <Button
            className={classes.btnAction}
            style={{
              marginRight: 8,
              marginLeft: 12,
            }}
            onClick={handleTransLeft}
          >
            <ArrowLeftIcon htmlColor={colors.ink[0]} />
          </Button>
        )}
        <Box className={classes.orderListWrap}>
          <div
            id="tabList"
            ref={ref}
            className={classes.listOrder}
            style={{
              left: trans,
            }}
          >
            {tabs.map((tab, index) => {
              return (
                <Tab
                  key={index}
                  active={tab.tabId === currentTab}
                  onClick={() => {
                    dispatch(updateCurrentTab(tab.tabId));
                  }}
                  lable={`n ${tab.tabIndex + 1}`}
                  onRemove={() => {
                    dispatch(removeTab(tab.tabId));
                  }}
                />
              );
            })}
          </div>
        </Box>
        {!!showAction && (
          <Button
            className={classes.btnAction}
            style={{
              marginLeft: 8,
            }}
            onClick={handleTransRight}
          >
            <ArrowRightIcon htmlColor={colors.ink[0]} />
          </Button>
        )}
        <IconButton
          id="btnAddOrderTab"
          onClick={() => {
            dispatch(addTab());
          }}
          className={classes.btnAdd}
        >
          <PlusCircleOutlineIcon />
        </IconButton>
      </Box>
    </Fragment>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
  tabs: state.tabList.tabs,
  currentTab: state.currentTab,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default connector(TabOrder);
