import React, { ReactNode, memo } from "react";
import NumberFormat from "react-number-format";
import {
  Box,
  InputLabel,
  OutlinedTextFieldProps,
  TextField,
  TextFieldProps,
} from "@material-ui/core";
import { ReactElement } from "react";
import clsx from "clsx";
import { AppState } from "../../store";
import { connect, ConnectedProps } from "react-redux";
import { useStyles } from "./TextInput.style";
import { EditIcon } from "../SVG";
import { Node } from "typescript";
import PosTypography, {
  TypographySize,
} from "../typography/PosTypography/PosTypography";
export interface TextInputProps extends OutlinedTextFieldProps, PropsFromRedux {
  width?: number | string;
  textLabel?: string;
  labelIcon?: ReactNode;
  labelColor?: string;
  lablelTextSize?: TypographySize;
}
// no c hng th lm debounceTimeout
const TextInput = memo(
  React.forwardRef((props: TextInputProps) => {
    const {} = props;
    const classes = useStyles(props)();
    const {
      width,
      textLabel,
      labelIcon,
      labelColor,
      lablelTextSize,
      ...textFieldProps
    } = props;

    return (
      <Box
        style={{
          display: "flex",
          flexDirection: "column",
          width: width ?? 200,
        }}
      >
        {textLabel && (
          <Box className={classes.label}>
            {labelIcon}
            {
              <PosTypography
                textColor={labelColor}
                size={lablelTextSize}
                className={classes.labelText}
              >
                {textLabel}
              </PosTypography>
            }
          </Box>
        )}
        <TextField
          {...textFieldProps}
          variant={props.variant ?? "outlined"}
          className={clsx(classes.root, props.className)}
        />
      </Box>
    );
  })
);

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default connector(TextInput);
