import { makeStyles, Theme } from "@material-ui/core";
import colors from "../../../../../../theme/statics/colors";

const useStyles = makeStyles((theme: Theme) => {
  return {
    searchInput: {
      color: colors.text[100],
      "& .MuiAutocomplete-inputRoot .MuiAutocomplete-input": {
        padding: 0,
        caretColor: colors.primary[100],
        lineHeight: "20px",
        fontSize: 14,
        height: 24,
        borderRight: `1px solid ${colors.primary[20]}`,
      },
      "& .MuiAutocomplete-hasPopupIcon .MuiAutocomplete-inputRoot, .MuiAutocomplete-hasClearIcon .MuiAutocomplete-inputRoot":
        {
          padding: 0,
        },
      "& .MuiOutlinedInput-notchedOutline": {
        borderColor: colors.background.main,
      },
      "& :hover .MuiOutlinedInput-notchedOutline": {
        borderColor: colors.primary[100],
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        borderWidth: 1,
        borderColor: colors.primary[100],
      },
      "& .MuiOutlinedInput-input::placeholder": {
        color: colors.text[50],
        opacity: 1,
      },
    },
    inputRootCustomer: {
      background: colors.background.main,
      height: 40,
      borderRadius: 32,
      "&.MuiAutocomplete-inputRoot": {
        padding: " 0 51px 0 16px   !important",
      },
      "& .MuiAutocomplete-clearIndicator": {
        display: "none",
      },
    },
    btnAddCustomer: {
      padding: 0,
      position: "absolute",
      right: 16,
      top: "50%",
      transform: "translateY(-50%)",
    },
    paperCustomer: {
      border: `1px solid ${colors.ink[10]}`,
      "& .MuiAutocomplete-listbox .MuiAutocomplete-option": {
        padding: " 8px 16px 0 16px",
      },
      boxShadow: "none",
      "& .MuiAutocomplete-option": {},
      "& .MuiAutocomplete-option[data-focus='true']": {
        backgroundColor: colors.ink[5],
      },
      "& .MuiAutocomplete-listbox": {
        "& li:last-child > div": {
          borderBottom: "none",
        },
        maxHeight: 280,
        "&::-webkit-scrollbar": {
          width: 4,
        },
        "&::-webkit-scrollbar-thumb": {
          backgroundColor: colors.ink[10],
          borderRadius: 12,
          "&:hover": {
            backgroundColor: colors.ink[20],
          },
        },
      },
    },
    listbox: {
      padding: 0,
      maxHeight: "300px",
      overflow: "auto",
      "& .MuiAutocomplete-option[aria-disabled='true']": {
        opacity: 1,
      },
    },
    searchCustomerOption: {
      boxSizing: "border-box",
      "&:hover .customer-name": {
        color: theme.palette.primary.main,
      },
      "& .search-customer-content": {
        width: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        "&.add-customer-btn": {
          display: "flex",
          flexDirection: "row",
          margin: "-8px -10px",
          justifyContent: "flex-start",
        },
        "&.add-customer-btn .icon-add-customer": {
          width: "55px",
        },
      },
      "& .customer-name": {
        whiteSpace: "nowrap",
        overflow: "hidden",
        textOverflow: "ellipsis",
      },
      "& .phone-number": {
        color: theme.palette.secondary.main,
      },
    },
    noOptions: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      height: 68,
    },
    loading: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      height: 68,
    },
  };
});
export default useStyles;
