import { memo } from "react";
import { connect, ConnectedProps } from "react-redux";
import { SapoLogoIcon } from "../../../../../../../../components/SVG";
import ImageDefault from "../../../../../../../../components/SVG/ImageDefault";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../../../components/typography/PosTypography/PosTypography";
import { VariantResponse } from "../../../../../../../../external-services/api/models/VariantResponse";
import { getVariantImageSrc } from "../../../../../../../../helpers/variantHelpers";
import { AppState } from "../../../../../../../../store";
import { formatNumber } from "../../../../../../../../utils/numberUtils";
import { formatPriceWithVNDCurrency } from "../../../../../../../../utils/priceUtils";
import useStyles from "./VariantOption.styles";

export type VariantOptionProps = {
  variant: VariantResponse;
};

const VariantOption = (props: VariantOptionProps & PropsFromRedux) => {
  const { variant } = props;
  const classes = useStyles();

  const imageSrc = getVariantImageSrc(variant);
  const hasImage = !!imageSrc && imageSrc !== "";

  return (
    <div className={classes.variant} data-variant-option>
      <div className={classes.variantImage}>
        {hasImage ? (
          <img src={imageSrc} />
        ) : (
          <SapoLogoIcon style={{ width: 26, height: 26 }} />
        )}
      </div>
      <div className={classes.variantName}>
        <div className="ellipsis">
          <PosTypography size={TEXTSIZE.size14}>{variant.name}</PosTypography>
        </div>
        <PosTypography size={TEXTSIZE.size14}>
          <span className={classes.sku}>{variant.sku}</span>
        </PosTypography>
      </div>
      <div className={classes.variantPrice}>
        <div className={classes.price}>
          {formatPriceWithVNDCurrency(variant.price)}
        </div>
        {variant.inventoryManagement && variant.inventoryManagement !== "" && (
          <div className={classes.quantity}>
            SL: {formatNumber(variant.inventoryQuantity ?? 0)}
          </div>
        )}
      </div>
    </div>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(VariantOption));
