import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Box,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import PosButton from "../../../../../../components/buttons/PosButton/PosButton";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../components/typography/PosTypography/PosTypography";
import colors from "../../../../../../theme/statics/colors";
import useStyles from "./PopupPayment.style";
import { TransitionProps } from "@material-ui/core/transitions";
import Slide from "@material-ui/core/Slide";
import PosToggle from "../../../../../../components/toggle/PosToggle";
import ToggleButtonGroup from "@material-ui/lab/ToggleButtonGroup";
import PaymentToggleButton from "../PaymentToggleButton/PaymentToggleButton";
import { formatPrice } from "../../../../../../utils/priceUtils";
import PosNumberInput from "../../../../../../components/numberInput/PosNumberInput";
import { ToggleButton } from "@material-ui/lab";
import TransferGraphic from "../../../../../../components/Graphics/TransferGraphic";
import OCBLogo from "../../../../../../components/Graphics/OCBLogo";
import imgQRPath from "../../../../../../utils/images/imageQR.png";
import MOMOLogo from "../../../../../../components/Graphics/MOMOLogo";
import VNPayLogo from "../../../../../../components/Graphics/VNPayLogo";
import { AppState } from "../../../../../../store";
import { connect, ConnectedProps } from "react-redux";
import { PaymentMethodResponse } from "../../../../../../external-services/api/models/PaymentMethodResponse";
import TenantApiService from "../../../../../../external-services/api/TenantApiServices";
import CustomerApiService from "../../../../../../external-services/api/CustomerApiServices";
import { CustomerPaymentAccountResponse } from "../../../../../../external-services/api/models/CustomerPaymentAccountResponse";
import { OrderPaymentModel } from "../../../../../../models/OrderPaymentModel";
interface PopupPaymentProps {
  open: boolean;
  handleClose: () => void;
}

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & { children?: React.ReactElement<any, any> },
  ref: React.Ref<unknown>
) {
  return <Slide direction="left" ref={ref} {...props} />;
});

const PopupPayment = (props: PopupPaymentProps & PropsFromRedux) => {
  const { open, handleClose, hotkey, customer } = props;
  const [isSinglePayment, setIsSinglePayment] = useState<boolean>(true);
  const [payments, setPayments] = useState<OrderPaymentModel[]>([]);
  const [selectedPaymentMethodIds, setSelectedPaymentMethodIds] = useState<
    number[]
  >([]);
  const [paymentAccounts, setPaymentAccounts] = useState<
    CustomerPaymentAccountResponse[]
  >([]);
  const [card, setCard] = useState<CustomerPaymentAccountResponse>();
  const classes = useStyles();
  const [paymentAmount, setPaymentAmount] = useState<number>(1234500);
  const [paymentMethods, setPaymentMethods] = useState<PaymentMethodResponse[]>(
    []
  );

  useEffect(() => {
    setTimeout(focusOnPaidAmountInput, 0);
  }, [open]);

  useEffect(() => {
    (async () => {
      const paymentMethods = await TenantApiService.getPaymentMethods();
      setPaymentMethods(paymentMethods);
    })();
  }, []);

  useEffect(() => {
    if (paymentMethods.length > 0) {
      const cashPaymentMethod = paymentMethods.find((pm) => pm.type === "cash");
      if (cashPaymentMethod) {
        // todo: genPayment
        setSelectedPaymentMethodIds([cashPaymentMethod.id]);
      } else {
        // todo: genPayment
        setSelectedPaymentMethodIds([paymentMethods[0].id]);
      }
    }
  }, [paymentMethods.length, isSinglePayment]);

  useEffect(() => {
    (async () => {
      if (customer) {
        const accounts = await CustomerApiService.getCustomerPaymentAccounts(
          customer.id
        );
        setPaymentAccounts(accounts);
        if (accounts.length > 0) {
          setCard(accounts[0]);
        }
      } else {
        setPaymentAccounts([]);
      }
    })();
  }, [customer]);

  useEffect(() => {}, []);

  useEffect(() => {
    console.log("xx payment change", payments);
  }, [payments]);

  const focusOnPaidAmountInput = () => {
    document.getElementById("inputPaidAmount")?.focus();
  };

  const handleChangePayments = (
    event: React.MouseEvent<HTMLElement>,
    value: any
  ) => {
    if (isSinglePayment) {
      setSelectedPaymentMethodIds([value as number]);
      scrollToPaymentMethodOfIndex(
        paymentMethods.findIndex((pm) => pm.id === value)
      );
    } else {
      setSelectedPaymentMethodIds(value as number[]);
    }
  };
  const handleChangeCard = (
    event: React.MouseEvent<HTMLElement>,
    newFormats: CustomerPaymentAccountResponse
  ) => {
    setCard(newFormats);
    console.log("card", card);
  };
  const handlePickSuggestedAmount = (
    event: React.MouseEvent<HTMLElement>,
    val: number
  ) => {
    setPaymentAmount(val);
  };

  const listPrice = [
    1234500, 1234000, 1244000, 1250000, 1300000, 1400000, 1500000, 2000000,
  ];

  const isSelectingNextPaymentMethod = (
    e: React.KeyboardEvent<HTMLElement>
  ) => {
    return (
      isSinglePayment &&
      e.code === hotkey.order.paymentPopup.selectNextPaymentMethod
    );
  };
  const isFindingNextPaymentMethod = (e: React.KeyboardEvent<HTMLElement>) => {
    return (
      !isSinglePayment &&
      e.code === hotkey.order.paymentPopup.selectNextPaymentMethod
    );
  };

  const selectNextPaymentMethod = (e: React.KeyboardEvent<HTMLElement>) => {
    e.stopPropagation();
    e.preventDefault();
    let paymentMethodItemElm = document.querySelector(
      "#paymentMethodList > [data-selected-payment-method='true']"
    );
    if (paymentMethodItemElm) {
      const nextElementSibling = paymentMethodItemElm.nextElementSibling;
      if (nextElementSibling?.hasAttribute("data-selected-payment-method")) {
        (nextElementSibling.firstElementChild as HTMLElement)?.click();
        scrollToPaymentMethod(nextElementSibling);
      } else {
        const firstChild =
          document.getElementById("paymentMethodList")?.firstElementChild;
        (firstChild?.firstElementChild as HTMLElement)?.click();
        scrollToPaymentMethod(firstChild);
      }
    }
  };

  const focusNextPaymentMethod = (e: React.KeyboardEvent<HTMLElement>) => {
    e.stopPropagation();
    e.preventDefault();
    let paymentMethodItemElm = document.querySelector(
      `#paymentMethodList > [data-payment-method-focus='true']`
    );
    if (paymentMethodItemElm === null) {
      paymentMethodItemElm = document.querySelectorAll(
        "#paymentMethodList > [data-selected-payment-method='true']"
      )[0];
      if (paymentMethodItemElm === undefined) {
        paymentMethodItemElm = document.querySelectorAll(
          "#paymentMethodList > [data-payment-method-focus='false']"
        )[0];
      }
    }
    if (paymentMethodItemElm) {
      const nextElementSibling = paymentMethodItemElm.nextElementSibling;
      if (nextElementSibling?.hasAttribute("data-payment-method-focus")) {
        (nextElementSibling.firstElementChild as HTMLElement)?.focus();
        scrollToPaymentMethod(nextElementSibling);
      } else {
        const firstChild =
          document.getElementById("paymentMethodList")?.firstElementChild;
        (firstChild?.firstElementChild as HTMLElement)?.focus();
        scrollToPaymentMethod(firstChild);
      }
    }
  };

  const scrollToPaymentMethod = (
    paymentMethodElm: Element | null | undefined
  ) => {
    let nextIndex = Array.prototype.indexOf.call(
      document.getElementById("paymentMethodList")?.children,
      paymentMethodElm
    );
    scrollToPaymentMethodOfIndex(nextIndex);
  };

  const scrollToPaymentMethodOfIndex = (index: number) => {
    const boxWidth = 100;
    const viewItemCount = 4;
    const containerWidth = 563;
    if (
      index >= 0 &&
      document.getElementById("paymentMethodList")?.parentElement
    ) {
      const boxScrollLeft =
        document.getElementById("paymentMethodList")!.parentElement!.scrollLeft;
      const isItemInsideView =
        index * boxWidth >= boxScrollLeft &&
        (index + 1) * boxWidth <= boxScrollLeft + containerWidth;
      if (isItemInsideView) return;
      if ((index + 1) * boxWidth > boxScrollLeft + containerWidth) {
        const scroll = (index - viewItemCount) * boxWidth;
        document.getElementById(
          "paymentMethodList"
        )!.parentElement!.scrollLeft = scroll;
      } else {
        const scroll = index * boxWidth - 25;
        document.getElementById(
          "paymentMethodList"
        )!.parentElement!.scrollLeft = scroll;
      }
    }
  };

  const isMovingToNextPaymentMethod = (e: React.KeyboardEvent<HTMLElement>) => {
    return (
      document.getElementById("suggestedPaidAmounts") !== null &&
      e.code === hotkey.order.paymentPopup.moveToNextPaymentMethod
    );
  };

  const selectNextSuggestedPaidAmount = (
    e: React.KeyboardEvent<HTMLElement>
  ) => {
    e.stopPropagation();
    e.preventDefault();
    const currentSelectedElm = document.querySelector(
      "#suggestedPaidAmounts [data-suggested-paid-amount='true']"
    );
    if (currentSelectedElm) {
      const nextElm = currentSelectedElm.nextElementSibling;
      if (nextElm?.hasAttribute("data-suggested-paid-amount")) {
        (nextElm as HTMLElement)?.click();
      } else {
        (
          document.getElementById("suggestedPaidAmounts")
            ?.firstElementChild as HTMLElement
        )?.click();
      }
    }
  };

  const isMovingToPreviousPaymentMethod = (
    e: React.KeyboardEvent<HTMLElement>
  ) => {
    return (
      document.getElementById("suggestedPaidAmounts") !== null &&
      e.code === hotkey.order.paymentPopup.moveToPreviousPaymentMethod
    );
  };

  const selectPreviousSuggestedPaidAmount = (
    e: React.KeyboardEvent<HTMLElement>
  ) => {
    e.stopPropagation();
    e.preventDefault();
    const currentSelectedElm = document.querySelector(
      "#suggestedPaidAmounts [data-suggested-paid-amount='true']"
    );
    if (currentSelectedElm) {
      const prevElm = currentSelectedElm.previousElementSibling;
      if (prevElm?.hasAttribute("data-suggested-paid-amount")) {
        (prevElm as HTMLElement)?.click();
      } else {
        (
          document.getElementById("suggestedPaidAmounts")
            ?.lastElementChild as HTMLElement
        )?.click();
      }
    }
  };

  const isMovingToNextPaymentAccount = (
    e: React.KeyboardEvent<HTMLElement>
  ) => {
    return (
      document.getElementById("paymentAccountList") !== null &&
      e.code === hotkey.order.paymentPopup.moveToNextPaymentAccount
    );
  };

  const selectNextPaymentAccount = (e: React.KeyboardEvent<HTMLElement>) => {
    e.stopPropagation();
    e.preventDefault();
    const currentSelectedElm = document.querySelector(
      "#paymentAccountList [data-payment-account='true']"
    );
    if (currentSelectedElm) {
      const nextElm = currentSelectedElm.nextElementSibling;
      if (nextElm?.hasAttribute("data-payment-account")) {
        (nextElm as HTMLElement)?.click();
      } else {
        (
          document.getElementById("paymentAccountList")
            ?.firstElementChild as HTMLElement
        )?.click();
      }
    }
  };

  const isMovingToPreviousPaymentAccount = (
    e: React.KeyboardEvent<HTMLElement>
  ) => {
    return (
      document.getElementById("paymentAccountList") !== null &&
      e.code === hotkey.order.paymentPopup.moveToPreviousPaymentAccount
    );
  };

  const selectPreviousPaymentAccount = (
    e: React.KeyboardEvent<HTMLElement>
  ) => {
    e.stopPropagation();
    e.preventDefault();
    const currentSelectedElm = document.querySelector(
      "#paymentAccountList [data-payment-account='true']"
    );
    if (currentSelectedElm) {
      const prevElm = currentSelectedElm.previousElementSibling;
      if (prevElm?.hasAttribute("data-payment-account")) {
        (prevElm as HTMLElement)?.click();
      } else {
        (
          document.getElementById("paymentAccountList")
            ?.lastElementChild as HTMLElement
        )?.click();
      }
    }
  };

  const isCompletingOrder = (e: React.KeyboardEvent<HTMLElement>) => {
    return e.code === hotkey.order.createOrder;
  };

  const isClosing = (e: React.KeyboardEvent<HTMLElement>) => {
    return e.code === hotkey.order.deleteOrder;
  };

  const handlePopupKeydown = (e: React.KeyboardEvent<HTMLElement>) => {
    console.log("xx payment ev", e.code);
    if (isSelectingNextPaymentMethod(e)) {
      selectNextPaymentMethod(e);
    }
    if (isFindingNextPaymentMethod(e)) {
      focusNextPaymentMethod(e);
      e.stopPropagation();
    }
    if (isMovingToNextPaymentMethod(e)) {
      selectNextSuggestedPaidAmount(e);
    } else if (isMovingToPreviousPaymentMethod(e)) {
      selectPreviousSuggestedPaidAmount(e);
    } else if (isMovingToNextPaymentAccount(e)) {
      selectNextPaymentAccount(e);
    } else if (isMovingToPreviousPaymentAccount(e)) {
      selectPreviousPaymentAccount(e);
    } else if (isCompletingOrder(e)) {
      e.stopPropagation();
      e.preventDefault();
      (
        document.getElementById("btnCompleteOrder")
          ?.firstElementChild as HTMLElement
      )?.click();
    } else if (isClosing(e)) {
      e.stopPropagation();
      handleClose();
    }
  };

  const renderPaymentMethodIcon = (paymentMethodCode: string) => {
    if (paymentMethodCode === "bank_transfer") return <TransferGraphic />;
    if (paymentMethodCode === "ocb_qr") return <OCBLogo />;
    if (paymentMethodCode === "vnpay") return <VNPayLogo />;
    if (paymentMethodCode === "momo") return <MOMOLogo />;
    return undefined;
  };

  return (
    <Dialog
      id="paymentPopup"
      open={open}
      onClose={handleClose}
      TransitionComponent={Transition}
      fullWidth
      maxWidth="sm"
      classes={{
        paper: classes.paper,
      }}
      onKeyDown={handlePopupKeydown}
    >
      <DialogTitle disableTypography className={classes.dialogTitle}>
        <PosTypography size={TEXTSIZE.size22} fontWeight="bold">
          Thanh ton
        </PosTypography>
        <Box>
          <PosTypography
            size={TEXTSIZE.size14}
            textColor={colors.text[90]}
            style={{ marginRight: 12 }}
          >
            Nhiu phng thc
          </PosTypography>
          <PosToggle
            // value={isSinglePayment}
            checked={!isSinglePayment}
            onChange={() => {
              setIsSinglePayment(!isSinglePayment);
            }}
          />
        </Box>
      </DialogTitle>

      <DialogContent className={classes.contentWrap}>
        {/* ng chn hnh thc thanh ton */}
        <Box className={classes.payment}>
          <ToggleButtonGroup
            value={selectedPaymentMethodIds}
            exclusive={isSinglePayment}
            onChange={handleChangePayments}
            id="paymentMethodList"
          >
            {paymentMethods
              // .filter((pm) => {
              //   if (!customer && pm.type !== "cash") return false;
              //   return true;
              // })
              .map((pm, index) => (
                <PaymentToggleButton
                  key={index}
                  isFocus={selectedPaymentMethodIds.some((id) => id === pm.id)}
                  isSelected={selectedPaymentMethodIds.some(
                    (id) => id === pm.id
                  )}
                  isExclusive={isSinglePayment}
                  value={pm.id}
                  label={pm.name}
                  icon={renderPaymentMethodIcon(pm.code)}
                />
              ))}
          </ToggleButtonGroup>
        </Box>
        <hr className={classes.separatorLine} />
        <Box className={classes.paymentInfor}>
          {/* Ti khon ngn hng*/}
          {isSinglePayment &&
            paymentAccounts.length > 0 &&
            paymentMethods.some(
              (pm) =>
                pm.id === selectedPaymentMethodIds[0] &&
                pm.type === "bank_transfer"
            ) && (
              <Box className={classes.cardWrap}>
                <ToggleButtonGroup
                  id="paymentAccountList"
                  value={card as CustomerPaymentAccountResponse}
                  exclusive
                  onChange={handleChangeCard}
                >
                  {paymentAccounts.map((c, index) => {
                    return (
                      <ToggleButton
                        key={index}
                        selected={card && card.id === c.id}
                        classes={{
                          root: classes.cardItem,
                          selected: classes.cardItemSelected,
                        }}
                        value={c}
                        data-payment-account={card?.id === c.id}
                      >
                        <img src={imgQRPath} />
                        <Box className={classes.cardInfor}>
                          <PosTypography
                            size={TEXTSIZE.size18}
                            fontWeight={"medium"}
                            className={classes.accountName}
                          >
                            {c.bankName} {" - "} {c.accountName}
                          </PosTypography>
                          <PosTypography
                            size={TEXTSIZE.size18}
                            textColor={colors.text[70]}
                          >
                            {c.accountNumber}
                          </PosTypography>
                        </Box>
                      </ToggleButton>
                    );
                  })}
                </ToggleButtonGroup>
              </Box>
            )}

          {/* Khch phi tr  - always */}
          <Box className={classes.lineItem}>
            <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
              Khch phi tr
            </PosTypography>
            <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
              {formatPrice(1000000)}
            </PosTypography>
          </Box>
          {/* tr im - always*/}
          <Box className={classes.lineItem}>
            <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
              Tr im (tng im: <b style={{ color: colors.text[100] }}>60</b>
              )
            </PosTypography>
            <Box>
              <PosNumberInput
                name="quantity"
                onChange={() => {}}
                classes={classes.pointInput}
              />
              <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
                im <span style={{ fontSize: 20, margin: "0 10px" }}>=</span>
                {formatPrice(1000000)}
              </PosTypography>
            </Box>
          </Box>
          {/* Tin khch a - singlePayment*/}
          {isSinglePayment && (
            <Box className={classes.lineItem} alignItems={"flex-end"}>
              <PosTypography
                size={TEXTSIZE.size16}
                fontWeight="medium"
                textColor={colors.text[100]}
              >
                Tin khch a
              </PosTypography>
              <PosNumberInput
                id="inputPaidAmount"
                name="receive"
                value={paymentAmount}
                onChange={(e) => {
                  setPaymentAmount(Number.parseInt(e.target.value));
                }}
                selectOnFocus={true}
                fontsize={24}
                fontWeight={500}
                style={{ width: 160 }}
              />
            </Box>
          )}
          {/* Suggest money  - singlePayment cash*/}
          {isSinglePayment &&
            paymentMethods.some(
              (pm) =>
                pm.id === selectedPaymentMethodIds[0] && pm.type === "cash"
            ) && (
              <Box>
                <ToggleButtonGroup
                  id="suggestedPaidAmounts"
                  value={paymentAmount}
                  exclusive
                  className={classes.suggest}
                  onChange={handlePickSuggestedAmount}
                >
                  {listPrice.map((price, index) => {
                    return (
                      <ToggleButton
                        key={index}
                        value={price}
                        classes={{
                          root: classes.suggestItem,
                          selected: classes.suggestSelected,
                        }}
                        data-suggested-paid-amount={price === paymentAmount}
                      >
                        <PosTypography textColor={colors.text[90]}>
                          {formatPrice(price)}
                        </PosTypography>
                      </ToggleButton>
                    );
                  })}
                </ToggleButtonGroup>
              </Box>
            )}
          {/* Tin tha - always*/}
          <Box className={classes.lineItem} alignItems={"flex-end"}>
            <PosTypography
              size={TEXTSIZE.size16}
              fontWeight="medium"
              textColor={colors.text[100]}
            >
              Tin tha
            </PosTypography>
            <PosTypography
              size={TEXTSIZE.size24}
              fontWeight="medium"
              textColor={colors.text[100]}
            >
              0
            </PosTypography>
          </Box>
        </Box>
      </DialogContent>

      <DialogActions className={classes.dialogFooter}>
        <Box display={"flex"}>
          <Box marginLeft={"auto"}>
            <PosButton
              id="btnCompleteOrder"
              customSize="big"
              style={{ width: 230 }}
              fontWeight={500}
              onClick={handleClose}
              color="primary"
            >
              Hon tt
            </PosButton>
          </Box>
        </Box>
      </DialogActions>
    </Dialog>
  );
};

const mapStateToProps = (state: AppState) => ({
  hotkey: state.hotkey,
  customer: state.customer[state.currentTab],
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default connector(PopupPayment);
