import { makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) => {
  return {
    root: {
      position: "relative",
      zIndex: 1,
    },
    searchInput: {
      width: 505,
      height: 40,
      display: "flex",
      flexDirection: "row",
      alignItems: "center",
      padding: "6px 8px 6px 16px",
      gap: 8,
      background: theme.palette.customColors.background.main,
      borderRadius: 36,
      marginRight: 10,
    },
    searchAuto: {
      height: 40,
      display: "flex",
      alignItems: "center",
      gap: 8,
      borderRadius: 36,
    },
    input: {
      width: 390,
      "& .MuiInputBase-root": {
        fontSize: 14,
      },
    },
    listbox: {
      padding: "0 !important",
    },
    popper: {
      // width: "100% !important",
      // top: 40,
      // left: 0,

      width: "505px !important",
      top: "4px !important",
      left: "9px !important",
    },
    paper: {
      width: "100%",
      border: `solid 1px ${theme.palette.customColors.ink[10]}`,
      boxShadow: "none",
    },
    option: {
      padding: "0 !important",

      "&.MuiAutocomplete-option[aria-selected='true']": {
        backgroundColor: "inherit",
      },
      "&.MuiAutocomplete-option[data-focus='true']": {
        backgroundColor: theme.palette.customColors.ink[5],
      },
    },
    addVariant: {
      display: "flex",
      width: "100%",
      padding: 12,
    },
    noVariants: {
      textAlign: "center",
      width: "100%",
      padding: 12,
      color: theme.palette.customColors.ink[70],
    },
  };
});

export default useStyles;
