import axios, { AxiosResponse } from "axios";
import {
  CustomerFilter,
  CustomerRequest,
  CustomerResponse,
} from "./models/CustomerModels";
import { CustomerPaymentAccountResponse } from "./models/CustomerPaymentAccountResponse";
import { getAxiosConfig } from "./config";

export default class CustomerApiService {
  static async getCustomers(
    filter?: CustomerFilter
  ): Promise<CustomerResponse[]> {
    const response = await axios.get<{ customers: CustomerResponse[] }>(
      `${process.env.REACT_APP_API_BASE_URL}/admin/customers.json`,
      {
        params: filter,
      }
    );
    return response.data.customers;
  }
  static async getCustomerPaymentAccounts(
    customerId: number
  ): Promise<CustomerPaymentAccountResponse[]> {
    return new Promise((resolve) => {
      setTimeout(() => {
        return resolve(
          samplePaymentAccounts.filter((a) => a.customerId === customerId)
        );
      }, 300);
    });
  }

  static async addCustomer(
    customer: CustomerRequest | null
  ): Promise<AxiosResponse<{ customer: CustomerResponse }>> {
    return axios.post(
      `${process.env.REACT_APP_API_BASE_URL}/admin/customers.json`,
      { customer }
      // { ...getAxiosConfig() }
    );
  }

  static async updateCustomer(
    customer: CustomerRequest | null
  ): Promise<AxiosResponse<{ customer: CustomerResponse }>> {
    return axios.put(
      `${process.env.REACT_APP_API_BASE_URL}/admin/customers/${customer?.id}.json`,
      { customer }
      // { ...getAxiosConfig() }
    );
  }
}

