import { makeStyles, Theme } from "@material-ui/core";
import { PosTabProps } from "./Tab";

const useStyles = (props: PosTabProps) => {
  const active = props.active;

  return makeStyles((theme: Theme) => {
    const textStyle = {
      overflow: "hidden",
      whiteSpace: "nowrap" as "nowrap",
      textOverflow: "ellipsis",
    };
    const root = {
      "&:focus::after": {
        content: "close-quote",
        height: "100%",
        width: "100%",
        zIndex: 1000,
        // padding: "0 12px",
        borderRadius: 12,
        backgroundColor: theme.palette.customColors.background.main,
        opacity: active ? 0 : 0.5,
        top: 0,
        left: 0,
      },
      backgroundColor: "transparent",
      outline: "none",
      border: 0,
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      width: 98,
      height: 46,
      padding: 12,
      borderRadius: "12px 12px 0  0",
      position: "relative" as "relative",
      cursor: "pointer",
      "& .MuiIconButton-root": {
        padding: 5,
      },
      "&::after": {
        content: "close-quote",
        height: "50%",
        width: active ? 0 : "1px",
        position: "absolute" as "absolute",
        right: 0,
        top: "25%",
        backgroundColor: theme.palette.customColors.ink[10],
      },
    };
    const leftCurve = {
      position: "absolute" as "absolute",
      bottom: 0,
      left: -12,
      height: "100%",
      width: 12,
      background: theme.palette.customColors.background.main,
      display: active ? "block" : "none",
      "&::before": {
        content: "close-quote",
        top: 0,
        left: 0,
        position: "absolute" as "absolute",
        width: "100%",
        height: "100%",
        background: theme.palette.customColors.background.white,
        borderRadius: "0 0 12px 0",
      },
    };
    const rightCurve = {
      position: "absolute" as "absolute",
      top: 0,
      right: -12,
      height: "100%",
      width: 12,
      zIndex: 1,
      background: theme.palette.customColors.background.main,
      display: active ? "block" : "none",
      "&::before": {
        content: "close-quote",
        top: 0,
        right: 0,
        position: "absolute" as "absolute",
        width: "100%",
        height: "100%",
        background: theme.palette.customColors.background.white,
        zIndex: 1,
        borderRadius: "0 0  0 12px",
      },
    };

    const activeTab = active
      ? {
          background: theme.palette.customColors.background.main,
          "&:hover": {
            backgroundColor: theme.palette.customColors.background.main,
          },
        }
      : {
          "&:hover": {
            backgroundColor: "transparent",
          },
        };
    const closeIcon = {
      zIndex: 1,
    };
    return {
      root,
      activeTab,
      leftCurve,
      rightCurve,
      textStyle,
      closeIcon,
    };
  });
};
export default useStyles;
