import { Box } from "@material-ui/core";
import { memo, useState } from "react";
import { connect, ConnectedProps, useDispatch } from "react-redux";
import PosButton from "../../../../components/buttons/PosButton/PosButton";
import TextInput from "../../../../components/input/TextInput";
import { EditIcon } from "../../../../components/SVG";
import PosTypography, {
  TEXTSIZE,
} from "../../../../components/typography/PosTypography/PosTypography";
import { AppState } from "../../../../store";
import colors from "../../../../theme/statics/colors";
import { formatPrice } from "../../../../utils/priceUtils";
import { removeCustomer } from "../../slices/customer/slice";
import useStyles from "./BoxOrderSummary.styles";
import CustomerInfor from "./components/CustomerInfor/CustomerInfor";
import CustomerSearch from "./components/CustomerSearch/CustomerSearch";
import PopupCustomer from "./components/PopupCustomer/PopupCustomer";
import PopupPayment from "./components/PopupPayment/PopupPayment";

const BoxOrderSummary = (props: PropsFromRedux) => {
  const classes = useStyles();
  const { customer, currentTab, hotkey } = props;
  const [openDialogCustomer, setOpenDialogCustomer] = useState<boolean>(false);
  const [openDialogPayment, setOpenDialogPayment] = useState<boolean>(false);
  const [isCreate, setIsCreate] = useState<boolean>(false);
  const dispatch = useDispatch<any>();

  const handleCloseDialogCustomer = () => {
    setOpenDialogCustomer(false);
  };
  const handleOpenDialogCutomer = () => {
    setOpenDialogCustomer(true);
  };
  const handleCloseDialogPayment = () => {
    setOpenDialogPayment(false);
  };
  const handleOpenDialogPayment = () => {
    setOpenDialogPayment(true);
  };

  return (
    <div className={classes.root}>
      <Box
        style={{
          padding: "12px 16px",
          borderBottom: `1px solid ${colors.ink[10]}`,
        }}
      >
        {customer ? (
          <CustomerInfor
            openDialog={() => {
              setIsCreate(false);
              handleOpenDialogCutomer();
            }}
            customer={customer}
            onDelete={() => {
              dispatch(
                removeCustomer({
                  tabId: currentTab,
                })
              );
            }}
          />
        ) : (
          <CustomerSearch
            openDialog={() => {
              setIsCreate(true);
              handleOpenDialogCutomer();
            }}
          />
        )}
      </Box>
      <Box className={classes.billWrap}>
        <Box className={classes.billItem}>
          <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
            Tm tnh
          </PosTypography>
          <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
            {formatPrice(1000000)}
          </PosTypography>
        </Box>
        <Box className={classes.billItem}>
          <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
            VAT (10%)
          </PosTypography>
          <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
            {formatPrice(1000000)}
          </PosTypography>
        </Box>
        <Box className={classes.billItem}>
          <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
            Khuyn mi
          </PosTypography>
          <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
            {formatPrice(1000000)}
          </PosTypography>
        </Box>
        <Box
          className={classes.billItem}
          style={{
            borderBottom: `1px solid ${colors.ink[10]}`,
            paddingBottom: 10,
          }}
        >
          <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
            Chit khu
          </PosTypography>
          <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
            {formatPrice(1000000)}
          </PosTypography>
        </Box>
        <Box className={classes.billItem}>
          <PosTypography
            size={TEXTSIZE.size18}
            fontWeight="medium"
            textColor={colors.text[100]}
          >
            Khch phi tr
          </PosTypography>
          <PosTypography
            size={TEXTSIZE.size18}
            fontWeight="medium"
            textColor={colors.text[100]}
          >
            {formatPrice(1000000)}
          </PosTypography>
        </Box>
      </Box>
      <Box className={classes.note}>
        <TextInput
          labelIcon={
            <EditIcon
              style={{ width: 20, height: 20 }}
              htmlColor={colors.text[70]}
            />
          }
          textLabel="Ghi ch"
          placeholder="Nhp ghi ch n hng"
          labelColor={colors.text[70]}
          lablelTextSize={TEXTSIZE.size14}
          fullWidth
          width={"100%"}
          variant={"outlined"}
        />
      </Box>
      <Box className={classes.action}>
        <PosButton
          customType="outlined"
          customSize="big"
          style={{ width: 132, padding: "17px 15px", lineHeight: "20px" }}
          fontWeight={500}
        >
          In tm tnh
        </PosButton>
        <PosButton
          id="btnPayForOrder"
          customSize="big"
          fontWeight={500}
          style={{ width: 224, padding: "17px 15px", lineHeight: "20px" }}
          onClick={handleOpenDialogPayment}
        >
          Thanh ton ({hotkey.order.createOrder})
        </PosButton>
      </Box>

      <PopupCustomer
        open={openDialogCustomer}
        customer={customer}
        handleClose={handleCloseDialogCustomer}
        isCreate={isCreate}
        currentTab={currentTab}
      />
      <PopupPayment
        open={openDialogPayment}
        handleClose={handleCloseDialogPayment}
      />
    </div>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
  currentTab: state.currentTab,
  customer: state.customer[state.currentTab],
  hotkey: state.hotkey,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(BoxOrderSummary));
