import { Box } from "@material-ui/core";
import { memo } from "react";
import { connect, ConnectedProps } from "react-redux";
import { LocationIcon, UserIcon } from "../../../../../../components/SVG";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../components/typography/PosTypography/PosTypography";
import { AppState } from "../../../../../../store";
import useStyles from "./OrderFooter.styles";

const OrderFooter = (props: PropsFromRedux) => {
  const { theme } = props;
  const classes = useStyles();

  return (
    <Box
      width="100%"
      display="flex"
      justifyContent="space-between"
      paddingLeft="11px"
      paddingRight="16px"
      paddingTop="4px"
      paddingBottom="9px"
      className={classes.root}
    >
      <Box display="flex" gridGap={30}>
        <PosTypography
          size={TEXTSIZE.size14}
          textColor={theme.current.color.text[100]}
          style={{
            display: "flex",
            alignItems: "center",
          }}
        >
          <UserIcon
            width={18}
            height={18}
            htmlColor={theme.current.color.icon.secondary}
            style={{
              marginRight: 5,
            }}
          />
          minhtv@sapo.vn
        </PosTypography>
        <PosTypography
          size={TEXTSIZE.size14}
          textColor={theme.current.color.text[100]}
          style={{
            display: "flex",
            alignItems: "center",
          }}
        >
          <LocationIcon
            htmlColor={theme.current.color.icon.secondary}
            style={{
              marginRight: 5,
            }}
          />{" "}
          Kho i Cn
        </PosTypography>
      </Box>
      <PosTypography
        size={TEXTSIZE.size14}
        textColor={theme.current.color.text[70]}
      >
        Hotline h tr: 19006750
      </PosTypography>
    </Box>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(OrderFooter));
