import { Box, IconButton } from "@material-ui/core";
import { memo } from "react";
import { connect, ConnectedProps, useSelector } from "react-redux";
import { DeleteIcon, UserIcon } from "../../../../../../components/SVG";
import { AppState } from "../../../../../../store";
import useStyles from "./CustomerInfor.style";
import colors from "../../../../../../theme/statics/colors";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../components/typography/PosTypography/PosTypography";
import { CustomerResponse } from "../../../../../../external-services/api/models/CustomerModels";
import { getCustomerDisplayName } from "../../../../../../helpers/customerHelpers";

interface CustomerInforProps {
  openDialog: () => void;
  customer: CustomerResponse;
  onDelete: () => void;
}

const CustomerInfor = (props: CustomerInforProps) => {
  const classes = useStyles();
  const { customer, openDialog } = props;

  return (
    <Box className={classes.root}>
      <UserIcon width={24} height={24} />
      <Box className={classes.flexBox}>
        <Box className={classes.basicInfor} onClick={openDialog}>
          <PosTypography
            className={classes.nameInfor}
            textColor={colors.primary[100]}
          >
            {getCustomerDisplayName(customer)}
          </PosTypography>
          <PosTypography
            className={classes.phoneInfor}
            textColor={colors.primary[100]}
          >
            {customer.phone !== null ? " - " + customer.phone : ""}
          </PosTypography>
        </Box>
        <Box display={"flex"} alignItems="center">
          <Box display={"flex"}>
            <PosTypography textColor={colors.text[50]} size={TEXTSIZE.size14}>
              <span>Cng n: </span>
              <span style={{ color: colors.text[90] }}> 100,000</span>
            </PosTypography>
          </Box>
          <Box className={classes.partition}></Box>
          <Box display={"flex"}>
            <PosTypography textColor={colors.text[50]} size={TEXTSIZE.size14}>
              <span>im: </span>
              <span style={{ color: colors.text[90] }}> 60</span>
            </PosTypography>
          </Box>
        </Box>
      </Box>
      <IconButton style={{ padding: 0 }} onClick={() => props.onDelete()}>
        <DeleteIcon htmlColor={colors.primary[100]} />
      </IconButton>
    </Box>
  );
};

export default memo(CustomerInfor);
