import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Grid,
  Box,
} from "@material-ui/core";
import { useEffect, useState } from "react";
import PosButton from "../../../../../../components/buttons/PosButton/PosButton";
import TextInput from "../../../../../../components/input/TextInput";
import {
  ArrowDownDoubleIcon,
  ArrowUpDoubleIcon,
  BriefcaseIcon,
  CardIcon,
  CloseIcon,
  LocationIcon,
  PhoneIcon,
  UserIcon,
} from "../../../../../../components/SVG";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../components/typography/PosTypography/PosTypography";
import {
  CustomerModelView,
  CustomerRequest,
  CustomerResponse,
} from "../../../../../../external-services/api/models/CustomerModels";
import colors from "../../../../../../theme/statics/colors";
import useStyles from "./PopupCustomer.style";
import { Controller, useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { handleValidateCustomer } from "./CustomerValidate";
import SnackbarUtils from "../../../../../../utils/Snackbar/SnackbarUtilsConfigurator";
import CustomerApiService from "../../../../../../external-services/api/CustomerApiServices";
import { setCustomer } from "../../../../slices/customer/slice";
import { useDispatch } from "react-redux";
import { getMessageError } from "../../../../../../utils/Error";

interface PopupCustomerProps {
  open: boolean;
  customer: CustomerResponse;
  handleClose: () => void;
  isCreate?: boolean;
  currentTab: string;
}

const PopupCustomer = (props: PopupCustomerProps) => {
  const { customer, open, handleClose, isCreate, currentTab } = props;

  console.log("customer", customer);
  const [isShowAdvance, setIsShowAdvance] = useState<boolean>(false);
  const [btnOkLoading, setBtnOkLoading] = useState(false);
  const dispatch = useDispatch<any>();

  const [initialValues, setInitValues] = useState<CustomerModelView>({
    name: "",
  });
  const {
    control,
    handleSubmit,
    setValue,
    trigger,
    reset,
    getValues,
    watch,
    formState: { errors },
  } = useForm<CustomerModelView>({
    mode: "onSubmit",
    defaultValues: initialValues,
    shouldUnregister: false,
    resolver: yupResolver(handleValidateCustomer()),
  });

  useEffect(() => {
    if (customer && customer.id && (customer.firstName || customer.lastName)) {
      setValue("id", customer.id);
      setValue(
        "name",
        customer.firstName ?? "" + " " + customer.lastName ?? ""
      );
      // set more value here
      if (customer.phone) {
        setValue("phone", customer.phone);
      }
    } else {
      reset(initialValues);
    }
  }, [open, customer]);

  const onInvalid = (error: any) => {
    SnackbarUtils.error(error[Object.keys(error)[0]].message);
  };

  const handleValidateBeforeSubmit = () => {
    handleSubmit(
      (data) => (isCreate ? handleCreate(data) : handleUpdate(data)),
      onInvalid
    )();
  };

  const handleCreate = async (values: CustomerModelView) => {
    setBtnOkLoading(true);
    const req: CustomerRequest = {
      first_name: values.name,
    };
    try {
      const customerRes = await CustomerApiService.addCustomer(req);
      if (customerRes && customerRes.data.customer) {
        dispatch(
          setCustomer({
            tabId: currentTab,
            customer: customerRes.data.customer,
          })
        );
        handleClose();
      }
    } catch (error) {
      SnackbarUtils.error(getMessageError(error));
    } finally {
      setBtnOkLoading(false);
    }
  };

  const handleUpdate = async (values: CustomerModelView) => {
    if (values.id) {
      const req: CustomerRequest = {
        id: values.id,
        first_name: values.name,
        phone: values.phone,
      };
      try {
        const customerRes = await CustomerApiService.updateCustomer(req);
        if (customerRes && customerRes.data.customer) {
          dispatch(
            setCustomer({
              tabId: currentTab,
              customer: customerRes.data.customer,
            })
          );
          handleClose();
        }
      } catch (error) {
      } finally {
        setBtnOkLoading(false);
      }
    }
  };

  const ShowMoreIcon = () => {
    return (
      <ArrowDownDoubleIcon
        htmlColor={colors.primary[100]}
        style={{ width: 20, height: 20, marginLeft: 5 }}
      />
    );
  };
  const ShowLessIcon = () => {
    return (
      <ArrowUpDoubleIcon
        htmlColor={colors.primary[100]}
        style={{ width: 20, height: 20, marginLeft: 5 }}
      />
    );
  };

  const classes = useStyles();
  const handleShowAdvance = () => {
    setIsShowAdvance(!isShowAdvance);
  };
  return (
    <Dialog
      open={open}
      onClose={handleClose}
      fullWidth
      maxWidth="sm"
      classes={{
        paper: classes.paper,
      }}
    >
      <DialogTitle disableTypography className={classes.dialogTitle}>
        <PosTypography size={TEXTSIZE.size20} fontWeight="medium">
          {isCreate ? "Thm mi khch hng" : "Thng tin khch hng"}
        </PosTypography>
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={handleClose}
        >
          <CloseIcon htmlColor={colors.icon.secondary} />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.contentWrap}>
        <Grid container style={{ gap: "0 0" }}>
          <Grid item xs={6} className={classes.contentItem}>
            <Controller
              render={({ field }) => (
                <TextInput
                  {...field}
                  required
                  error={!!errors.name}
                  helperText={errors?.name?.message}
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Tn khch hng"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              )}
              control={control}
              name="name"
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <Controller
              render={({ field }) => (
                <TextInput
                  {...field}
                  required
                  error={!!errors.phone}
                  helperText={errors?.phone?.message}
                  labelIcon={
                    <PhoneIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="S in thoi"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              )}
              control={control}
              name="phone"
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <Controller
              render={({ field }) => (
                <TextInput
                  {...field}
                  required
                  error={!!errors.code}
                  helperText={errors?.code?.message}
                  labelIcon={
                    <CardIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="M khch hng"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              )}
              control={control}
              name="code"
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <Controller
              render={({ field }) => (
                <TextInput
                  {...field}
                  required
                  error={!!errors.employee}
                  helperText={errors?.employee?.message}
                  labelIcon={
                    <BriefcaseIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Nhn vin ph trch"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              )}
              control={control}
              name="employee"
            />
          </Grid>
          <Grid item xs={12} className={classes.contentItem}>
            <Controller
              render={({ field }) => (
                <TextInput
                  {...field}
                  required
                  error={!!errors.address}
                  helperText={errors?.address?.message}
                  labelIcon={
                    <LocationIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="a ch"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              )}
              control={control}
              name="address"
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <Controller
              render={({ field }) => (
                <TextInput
                  {...field}
                  required
                  error={!!errors.area}
                  helperText={errors?.area?.message}
                  labelIcon={
                    <LocationIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Khu vc"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              )}
              control={control}
              name="area"
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <Controller
              render={({ field }) => (
                <TextInput
                  {...field}
                  required
                  error={!!errors.warn}
                  helperText={errors?.warn?.message}
                  labelIcon={
                    <LocationIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Phng x"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              )}
              control={control}
              name="warn"
            />
          </Grid>
          {isShowAdvance && (
            <>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="V"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="i"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Ri"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Anh"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Lm"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
            </>
          )}
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogFooter}>
        <Box>
          {!isShowAdvance ? (
            <PosButton
              style={{ padding: 0 }}
              customSize="small"
              customType="text"
              onClick={handleShowAdvance}
              color="primary"
              iconElm={ShowMoreIcon}
              iconPosition="right"
            >
              M rng
            </PosButton>
          ) : (
            <PosButton
              style={{ padding: 0 }}
              customSize="small"
              customType="text"
              onClick={handleShowAdvance}
              color="primary"
              iconElm={ShowLessIcon}
              iconPosition="right"
            >
              Khum m rng na
            </PosButton>
          )}
        </Box>
        <Box display={"flex"}>
          <Box marginRight={"12px"}>
            <PosButton
              customSize="small"
              customType="outlined"
              onClick={handleClose}
              color="primary"
            >
              Thot
            </PosButton>
          </Box>
          <Box>
            <PosButton
              customSize="small"
              onClick={() => handleValidateBeforeSubmit()}
              color="primary"
            >
              {isCreate ? "Thm" : "Cp nht"}
            </PosButton>
          </Box>
        </Box>
      </DialogActions>
    </Dialog>
  );
};

export default PopupCustomer;
