import { Typography, TypographyProps } from "@material-ui/core";
import { memo } from "react";
import { connect, ConnectedProps, useSelector } from "react-redux";
import { AppState } from "../../../store";
import colors from "../../../theme/statics/colors";

export const TEXTSIZE = {
  size12: {
    fontSize: 12,
    lineHeight: "18px",
  },
  size14: {
    fontSize: 14,
    lineHeight: "22px",
  },
  size16: {
    fontSize: 16,
    lineHeight: "24px",
  },
  size18: {
    fontSize: 18,
    lineHeight: "26px",
  },
  size20: {
    fontSize: 20,
    lineHeight: "28px",
  },
  size22: {
    fontSize: 22,
    lineHeight: "30px",
  },
  size24: {
    fontSize: 24,
    lineHeight: "32px",
  },
  size26: {
    fontSize: 26,
    lineHeight: "34px",
  },
  size28: {
    fontSize: 28,
    lineHeight: "36px",
  },
  size30: {
    fontSize: 30,
    lineHeight: "38px",
  },
  size32: {
    fontSize: 32,
    lineHeight: "40px",
  },
};

export interface TypographySize {
  fontSize: number;
  lineHeight: string;
}

interface PosTypographyProps extends TypographyProps {
  fontWeight?: "regular" | "medium" | "bold";
  size?: TypographySize;
  children: React.ReactNode | React.ReactNode[];
  textColor?: string;
}

const getFontWeight = (fontWeight?: string) => {
  if (!fontWeight) {
    return 400;
  } else {
    switch (fontWeight) {
      case "regular":
        return 400;
      case "medium":
        return 500;
      case "bold":
        return 700;
      default:
        return "";
    }
  }
};

const PosTypography = (props: PosTypographyProps) => {
  const theme = useSelector((state: AppState) => state.theme);
  const {
    size,
    style,
    children,
    textColor,
    fontWeight,
    className,
    ...typoProps
  } = props;

  delete (typoProps as any).dispatch;
  delete (typoProps as any).theme;
  delete (typoProps as any).htmlColor;
  return (
    <Typography
      {...typoProps}
      component="span"
      style={{
        ...style,
        fontSize: size ? size.fontSize : "16px",
        lineHeight: size ? size.lineHeight : "24px",
        fontWeight: getFontWeight(fontWeight),
        color: textColor ?? colors.text[100],
      }}
      className={className}
    >
      {children}
    </Typography>
  );
};

export default memo(PosTypography);
