import { makeStyles } from "@material-ui/core";
import { Theme } from "@material-ui/core/styles";
import colors from "../../theme/statics/colors";
import { DropdownProps } from "./Dropdown";

export const useStyles = (props: DropdownProps) => {
  const size = props.size ?? "medium";

  return makeStyles((theme: Theme) => {
    const primayColor = theme.palette.customColors.primary[100];
    const borderColor = theme.palette.customColors.ink[10];
    const bgrDisable = theme.palette.customColors.ink[5];

    const menuItem = {
      "& .MuiListItem-button:hover": {
        background: bgrDisable,
      },
    };
    const boxItem = {
      width: "100%",
    };
    const menuItemChossen = {
      background: bgrDisable,
      "& .MuiListItem-button:hover": {
        background: bgrDisable,
      },
    };
    const selectBox = {
      "& .MuiInput-underline:before": {
        borderBottom: `1px solid ${borderColor}`,
      },
      "& .MuiInput-underline:hover:not(.Mui-disabled):before": {
        borderBottom: `1px solid ${borderColor}`,
      },
      "& .MuiInput-underline:after": {
        borderBottom: `1px solid ${primayColor}`,
      },
      "& .MuiSelect-outlined.MuiSelect-outlined": {
        paddingRight: 36,
      },
      "& .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${primayColor}`,
      },
      "& .MuiOutlinedInput-root": {
        borderRadius: 8,
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${primayColor}`,
      },
      "& .MuiOutlinedInput-input": {
        paddingTop: size === "small" ? "10px" : "14px",
        paddingBottom: size === "small" ? "10px" : "14px",
        paddingLeft: 12,
      },
    };
    const paper = {
      borderRadius: 8,
      marginTop: 4,
      maxHeight: 200,
      "&::-webkit-scrollbar": {
        width: 4,
      },
      "&::-webkit-scrollbar-thumb": {
        backgroundColor: borderColor,
        borderRadius: 12,
        "&:hover": {
          backgroundColor: colors.ink[20],
        },
      },
      boxShadow: "none",
      border: `1px solid ${borderColor}`,
      "& .MuiListItem-gutters": {
        padding: "0 12px",
        position: "relative",

        "&:after": {
          content: "",
          height: 1,
          width: "100%",
          borderBottom: "1px solid #000",
          display: "block",
        },
      },
      "& .MuiList-padding": {
        padding: 0,
      },
    };
    const root = {};
    const iconSelect = {};
    return {
      root,
      boxItem,
      paper,
      selectBox,
      iconSelect,
      menuItemChossen,
      menuItem,
    };
  });
};
