import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { variant2HotkeyConfig } from "./static-configs";
import { HotkeyConfigModel } from "./types";

const initialState: HotkeyConfigModel = variant2HotkeyConfig;

const tenantContextSlice = createSlice({
  name: "hotkeyConfig",
  initialState: initialState,
  reducers: {
    updateHotkeyConfig: (state, action: PayloadAction<HotkeyConfigModel>) => {
      state = action.payload;
    },
  },
});

const { actions, reducer } = tenantContextSlice;
export const { updateHotkeyConfig } = actions;
const hotkeyConfigReducer = reducer;
export default hotkeyConfigReducer;
