import { Box, IconButton } from "@material-ui/core";
import { memo, useState } from "react";
import { connect, ConnectedProps, useDispatch } from "react-redux";
import { calculateLineItemPrice } from "../../../../../../business-services/order-price-calculator";
import Dropdown from "../../../../../../components/dropdown/Dropdown";
import PosNumberInput from "../../../../../../components/numberInput/PosNumberInput";
import {
  ArrowDownIcon,
  ArrowRightIcon,
  DeleteIcon,
  MinusCircleThickIcon,
  PlusCircleThickIcon,
  SapoLogoIcon,
} from "../../../../../../components/SVG";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../components/typography/PosTypography/PosTypography";
import { OrderLineItemModel } from "../../../../../../models/OrderLineItemModel";
import { AppState } from "../../../../../../store";
import { formatPrice } from "../../../../../../utils/priceUtils";
import { columnLeftPaddings, columnWidths } from "../../BoxLineItems.styles";
import useStyles from "./OrderLineItem.styles";
import {
  changeLineItemCustomName,
  changeLineItemNote,
  removeLineItem,
  updateLineItemQuantity,
  updateLineItemUnitPrice,
} from "../../../../slices/order-line-items/slice";
import TextInput from "../../../../../../components/input/TextInput";

export type OrderLineItemProps = {
  lineItem: OrderLineItemModel;
};

const OrderLineItem = (props: OrderLineItemProps & PropsFromRedux) => {
  const { lineItem, theme, currentTab, hotkey } = props;
  const classes = useStyles();
  const dispatch = useDispatch<any>();

  const [testUnitValue, setTestUnitValue] = useState<string>("Gi");
  const rootElmId = `lineItem-${lineItem.id}`;
  const hasImage = !!lineItem.variant?.image && lineItem.variant.image !== "";
  const [expanded, setExpanded] = useState<boolean>(false);

  const findLineItemElement = (): Element | null | undefined => {
    return document.activeElement?.closest("[data-order-line-item]");
  };

  const isMovingNext = (e: React.KeyboardEvent<HTMLElement>) => {
    return e.key === hotkey.order.lineItem.moveNext;
  };

  const focusToNextOrderLineItem = (e: React.KeyboardEvent<HTMLElement>) => {
    e.stopPropagation();
    const lineItemElm = findLineItemElement();
    const nextSibling = lineItemElm?.nextElementSibling as HTMLElement;
    if (nextSibling) {
      nextSibling.focus();
    } else {
      const firstChild = lineItemElm?.parentElement
        ?.firstElementChild as HTMLElement;
      firstChild?.focus();
    }
  };

  const isMovingPrevious = (e: React.KeyboardEvent<HTMLElement>) => {
    return e.key === hotkey.order.lineItem.movePrevious;
  };

  const focusToPreviousOrderLineItem = (
    e: React.KeyboardEvent<HTMLElement>
  ) => {
    e.stopPropagation();
    const lineItemElm = findLineItemElement();
    const prevSibling = lineItemElm?.previousElementSibling as HTMLElement;
    if (prevSibling) {
      prevSibling.focus();
    } else {
      const lastChild = lineItemElm?.parentElement
        ?.lastElementChild as HTMLElement;
      lastChild?.focus();
    }
  };

  const isIncreasingQuantity = (e: React.KeyboardEvent<HTMLElement>) => {
    return e.code === hotkey.order.lineItem.increaseQuantity;
  };

  const increaseLineQuantity = () => {
    dispatch(
      updateLineItemQuantity({
        tabId: currentTab,
        lineItemId: lineItem.id,
        quantity: lineItem.quantity + 1,
      })
    );
  };

  const isDecreasingQuantity = (e: React.KeyboardEvent<HTMLElement>) => {
    return e.code === hotkey.order.lineItem.decreaseQuantity;
  };

  const decreaseLineQuantity = () => {
    dispatch(
      updateLineItemQuantity({
        tabId: currentTab,
        lineItemId: lineItem.id,
        quantity: lineItem.quantity - 1,
      })
    );
  };

  const isDeleting = (e: React.KeyboardEvent<HTMLElement>) => {
    return (
      document.activeElement === document.getElementById(rootElmId) &&
      e.code === hotkey.order.lineItem.delete
    );
  };

  const handleKeydown = (e: React.KeyboardEvent<HTMLElement>) => {
    if (isMovingNext(e)) {
      focusToNextOrderLineItem(e);
    } else if (isMovingPrevious(e)) {
      focusToPreviousOrderLineItem(e);
    } else if (isIncreasingQuantity(e)) {
      increaseLineQuantity();
    } else if (isDecreasingQuantity(e)) {
      decreaseLineQuantity();
    } else if (isDeleting(e)) {
      dispatch(
        removeLineItem({
          tabId: currentTab,
          lineItemId: lineItem.id,
        })
      );
    }
  };

  return (
    <Box
      id={rootElmId}
      className={classes.root}
      tabIndex={0}
      onKeyDown={handleKeydown}
      data-order-line-item
    >
      <Box display="flex" justifyContent="center">
        <Box
          flex="1 1 0"
          className={classes.colProduct}
          alignItems="center"
          display="flex"
          paddingRight="32px"
        >
          <Box textAlign="center" width={45} minWidth={45}>
            <IconButton
              size="small"
              onClick={() => {
                setExpanded(!expanded);
              }}
            >
              {expanded ? (
                <ArrowDownIcon width={20} height={20} />
              ) : (
                <ArrowRightIcon width={20} height={20} />
              )}
            </IconButton>
          </Box>
          <Box className={classes.variantImage}>
            {hasImage ? (
              <img src={lineItem!.variant!.image} />
            ) : (
              <SapoLogoIcon style={{ width: 25, height: 25 }} />
            )}
          </Box>
          {lineItem.variant ? (
            <Box minWidth="0" paddingLeft="12px">
              <div className="ellipsis">
                <PosTypography
                  size={TEXTSIZE.size16}
                  textColor={theme.current.color.text[100]}
                >
                  {lineItem.variant.name}
                </PosTypography>
              </div>
              <PosTypography
                size={TEXTSIZE.size14}
                textColor={theme.current.color.text[50]}
              >
                {lineItem.variant.sku}
                &nbsp;&bull;&nbsp;<i>Mc nh</i>
              </PosTypography>
            </Box>
          ) : (
            lineItem.isFreeForm && (
              <Box minWidth="0" paddingLeft="12px" flex="1 1 0">
                <TextInput
                  className={`underlined-textbox ${classes.customNameInput}`}
                  variant="outlined"
                  width="100%"
                  lablelTextSize={TEXTSIZE.size14}
                  value={lineItem.customName}
                  onChange={(e) => {
                    console.log("xx customName", e.target.value);
                    dispatch(
                      changeLineItemCustomName({
                        tabId: currentTab,
                        lineItemId: lineItem.id,
                        name: e.target.value,
                      })
                    );
                  }}
                />
              </Box>
            )
          )}
        </Box>
        <Box
          display="flex"
          width={columnWidths.unit}
          minWidth={columnWidths.unit}
          textAlign="right"
          alignItems="center"
          justifyContent="right"
        >
          {!lineItem.isFreeForm && (
            <Dropdown
              width={100}
              options={["Gi", "Thng", "Hp"]}
              renderOption={(option) => (
                <div
                  className={`dropdown-option ${classes.dropdownOption}${
                    option === testUnitValue || option === "Hp"
                      ? " not-underlined"
                      : ""
                  }`}
                >
                  {option}
                </div>
              )}
              renderValue={(value) => (
                <div
                  style={{
                    paddingRight: 8,
                  }}
                >
                  {value as any}
                </div>
              )}
              value={testUnitValue}
              onChange={(ev) => {
                ev.preventDefault();
                setTestUnitValue(ev.target.value as string);
              }}
              styles={{
                box: {
                  width: "100%",
                },
              }}
            />
          )}
        </Box>
        <Box
          display="flex"
          width={columnWidths.unitPrice}
          minWidth={columnWidths.unitPrice}
          textAlign="right"
          alignItems="center"
          justifyContent="right"
          paddingLeft={`${columnLeftPaddings.unitPrice}px`}
        >
          <PosNumberInput
            onChange={(ev) => {
              if (ev.target.value !== lineItem.variant?.price.toString()) {
                dispatch(
                  updateLineItemUnitPrice({
                    tabId: currentTab,
                    lineItemId: lineItem.id,
                    price: Number.parseInt(ev.target.value ?? 0),
                  })
                );
              }
            }}
            value={lineItem.unitPrice}
            decimalSeparator={true}
            decimalScale={3}
            styleInput={{
              textAlign: "right",
              fontSize: 16,
            }}
            selectOnFocus={true}
            max={99999999}
          />
        </Box>
        <Box
          display="flex"
          width={columnWidths.quantity}
          minWidth={columnWidths.quantity}
          textAlign="center"
          alignItems="center"
          justifyContent="center"
          paddingLeft={`${columnLeftPaddings.quantity}px`}
        >
          <IconButton
            className={classes.quantityBtn}
            onClick={() => {
              dispatch(
                updateLineItemQuantity({
                  tabId: currentTab,
                  lineItemId: lineItem.id,
                  quantity: lineItem.quantity - 1,
                })
              );
            }}
          >
            <MinusCircleThickIcon
              htmlColor={theme.current.color.ink[10]}
              iconColor={theme.current.color.icon.primary}
            />
          </IconButton>
          <PosNumberInput
            onChange={(event) => {
              if (event.target.value !== lineItem.quantity.toString()) {
                dispatch(
                  updateLineItemQuantity({
                    tabId: currentTab,
                    lineItemId: lineItem.id,
                    quantity: Number.parseInt(event.target.value),
                  })
                );
              }
            }}
            value={lineItem.quantity}
            decimalSeparator={true}
            decimalScale={3}
            styleInput={{
              textAlign: "center",
              width: 44,
              fontSize: 16,
            }}
            style={{
              marginLeft: 2,
              marginRight: 2,
            }}
            selectOnFocus={true}
            max={999999}
            onKeyDown={(e) => {
              if (e.key === hotkey.order.lineItem.increaseQuantity) {
                e.stopPropagation();
                dispatch(
                  updateLineItemQuantity({
                    tabId: currentTab,
                    lineItemId: lineItem.id,
                    quantity: lineItem.quantity + 1,
                  })
                );
              } else if (e.key === hotkey.order.lineItem.decreaseQuantity) {
                e.stopPropagation();
                dispatch(
                  updateLineItemQuantity({
                    tabId: currentTab,
                    lineItemId: lineItem.id,
                    quantity: lineItem.quantity - 1,
                  })
                );
              } else if (
                ["ArrowLeft", "ArrowRight", "Backspace"].includes(e.key)
              ) {
                e.stopPropagation();
              }
            }}
          />
          <IconButton
            className={classes.quantityBtn}
            onClick={() => {
              dispatch(
                updateLineItemQuantity({
                  tabId: currentTab,
                  lineItemId: lineItem.id,
                  quantity: lineItem.quantity + 1,
                })
              );
            }}
          >
            <PlusCircleThickIcon
              htmlColor={theme.current.color.ink[10]}
              iconColor={theme.current.color.icon.primary}
            />
          </IconButton>
        </Box>
        <Box
          display="flex"
          width={columnWidths.toAmount}
          minWidth={columnWidths.toAmount}
          textAlign="right"
          alignItems="center"
          justifyContent="end"
          fontWeight={500}
        >
          {formatPrice(calculateLineItemPrice(lineItem))}
        </Box>
        <Box
          width={columnWidths.delete}
          display="flex"
          textAlign="center"
          alignItems="center"
          justifyContent="center"
        >
          <IconButton
            className={classes.deleteBtn}
            onClick={() => {
              dispatch(
                removeLineItem({
                  tabId: currentTab,
                  lineItemId: lineItem.id,
                })
              );
            }}
          >
            <DeleteIcon htmlColor={theme.current.color.icon.secondary} />
          </IconButton>
        </Box>
      </Box>
      {expanded && (
        <Box
          display="flex"
          justifyContent="space-around"
          alignItems="center"
          paddingLeft="40px"
          paddingRight="27px"
          paddingTop="13px"
        >
          <Box
            width={270}
            display="flex"
            justifyContent="space-around"
            alignItems="end"
          >
            <Box width={234}>
              <div
                style={{
                  marginBottom: 4,
                }}
              >
                <PosTypography
                  size={TEXTSIZE.size14}
                  textColor={theme.current.color.text[70]}
                >
                  Thu
                </PosTypography>
              </div>
              <PosNumberInput
                classes={`outlined-textbox ${classes.taxInput}`}
                max={100}
                decimalSeparator={true}
                decimalScale={2}
                selectOnFocus={true}
                onChange={() => {}}
              />
            </Box>
            <Box paddingBottom="14px" height="30px">
              %
            </Box>
          </Box>
          <Box paddingLeft="14px" width="calc(100% - 250px)">
            <TextInput
              variant="outlined"
              textLabel="Ghi ch"
              placeholder="Nhp ghi ch vi mt hng"
              width="100%"
              lablelTextSize={TEXTSIZE.size14}
              labelColor={theme.current.color.text[70]}
              value={lineItem.note}
              onChange={(e) => {
                dispatch(
                  changeLineItemNote({
                    tabId: currentTab,
                    lineItemId: lineItem.id,
                    note: e.target.value,
                  })
                );
              }}
            />
          </Box>
        </Box>
      )}
    </Box>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
  currentTab: state.currentTab,
  hotkey: state.hotkey,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(OrderLineItem));
