import { makeStyles, Theme } from "@material-ui/core";
import { ColorVariantModel } from "@material-ui/core/styles/createPalette";
import { CreateCSSProperties } from "@material-ui/core/styles/withStyles";
import { CSSProperties } from "react";
import { PosButtonProps } from "./PosButton";

const useStyles = (props: PosButtonProps) => {
  const isBigSize = props.customSize === "big";
  const size = props.customSize;
  const isOutlined = props.customType === "outlined",
    isText = props.customType === "text";
  const padding = {
    vertical: size === "big" ? 18 : size === "medium" ? 12 : 8,
    horizontal: 16,
  };
  const fontWeight = props.fontWeight ?? 400;

  const fontSize =
    props.fontSize ?? props.style?.fontSize ?? (isBigSize ? 20 : 14);
  const lineHeight = isBigSize ? "28px" : "22px";

  return makeStyles((theme: Theme) => {
    let mainPallete: ColorVariantModel = theme.palette.customColors.primary;
    if (props.colorType === "destruction") {
      mainPallete = theme.palette.customColors.destruction;
    }
    let border = mainPallete[100];
    let borderHover = mainPallete[80];
    let borderFocus = mainPallete[50];
    let borderDisabled = theme.palette.customColors.ink[10];

    let backgroundColor = mainPallete[100];
    let backgroundColorHover = mainPallete[80];
    let backgroundColorFocus = mainPallete[50];
    let backgroundColorDisabled = theme.palette.customColors.ink[10];

    let textColor = theme.palette.customColors.ink[0];
    let textColorHover = textColor;
    let textColorFocus = textColor;
    let textColorDisabled = theme.palette.customColors.ink[50];

    if (isOutlined) {
      border = mainPallete[100];
      borderHover = mainPallete[80];
      borderFocus = border;
      borderDisabled = theme.palette.customColors.ink[50];

      backgroundColor = mainPallete[5];
      backgroundColorHover = mainPallete[20];
      backgroundColorFocus = mainPallete[10];

      textColor = mainPallete[100];
      textColorHover = textColor;
      textColorFocus = textColor;
    } else if (isText) {
      backgroundColor = theme.palette.customColors.background.white;
      backgroundColorFocus = backgroundColor;
      backgroundColorHover = backgroundColor;
      backgroundColorDisabled = backgroundColor;
      border = "transparent";
      borderHover = "transparent";
      borderFocus = "transparent";
      textColor = mainPallete[100];
      textColorHover = mainPallete[80];
      textColorFocus = mainPallete[50];
    }

    const root: CSSProperties | CreateCSSProperties<{}> = {
      border: "solid 1px",
      borderColor: border,
      backgroundColor: backgroundColor,
      color: textColor,
      fontSize: fontSize,
      lineHeight: lineHeight,
      padding: `${padding.vertical}px ${padding.horizontal}px`,
      "& .MuiButton-label": {
        fontSize: fontSize,
        lineHeight: lineHeight,
        fontWeight: fontWeight,
      },
      "&:hover": {
        backgroundColor: backgroundColorHover,
        color: textColorHover,
        borderColor: borderHover,
      },
      "&:focus": {
        backgroundColor: backgroundColorFocus,
        color: textColorFocus,
        borderColor: borderFocus,
      },
      "&:disabled": {
        backgroundColor: backgroundColorDisabled,
        borderColor: borderDisabled,
        color: textColorDisabled,
        boxShadow: "none",
        opacity: 0.5,
      },
    };

    return {
      root: root,
    };
  });
};

export default useStyles;
