import { Box, Grid } from "@material-ui/core";
import { connect, ConnectedProps, useDispatch } from "react-redux";
import PosButton from "../../../../../../components/buttons/PosButton/PosButton";
import {
  DeleteIcon,
  GiftIcon,
  PlusCircleIcon,
  VoucherIcon,
} from "../../../../../../components/SVG";
import { AppState } from "../../../../../../store";
import { getHotkeyDisplayName } from "../../../../../../utils/hotkeyUtils";
import {
  addLineItem,
  addServiceLineItem,
  removeAllLineItemsInTab,
} from "../../../../slices/order-line-items/slice";
import useStyles from "./ActionPanel.styles";

const ActionPanel = (props: PropsFromRedux) => {
  const classes = useStyles();
  const { hotkey, currentTab } = props;
  const dispatch = useDispatch<any>();

  return (
    <Box width="100%" paddingTop="16px" paddingLeft="3px" paddingRight="8px">
      <Box display="flex" justifyContent="space-evenly" alignItems="center">
        <Grid container>
          <Grid item xs={6} lg={3} className={classes.buttonContainer}>
            <PosButton
              id="btnAddService"
              onClick={() => {
                dispatch(
                  addServiceLineItem({
                    tabId: currentTab,
                  })
                );
              }}
              iconElm={PlusCircleIcon}
              customType="outlined"
              className={classes.button}
              fontSize={14}
              fontWeight={500}
            >
              Thm dch v ({getHotkeyDisplayName(hotkey.order.addService)})
            </PosButton>
          </Grid>
          <Grid item xs={6} lg={3} className={classes.buttonContainer}>
            <PosButton
              iconElm={GiftIcon}
              customType="outlined"
              className={classes.button}
              fontSize={14}
              fontWeight={500}
            >
              Khuyn mi ({getHotkeyDisplayName(hotkey.order.openPromotion)})
            </PosButton>
          </Grid>
          <Grid item xs={6} lg={3} className={classes.buttonContainer}>
            <PosButton
              iconElm={VoucherIcon}
              customType="outlined"
              className={classes.button}
              fontSize={14}
              fontWeight={500}
            >
              Chit khu n (
              {getHotkeyDisplayName(hotkey.order.openOrderDiscount)})
            </PosButton>
          </Grid>
          <Grid item xs={6} lg={3} className={classes.buttonContainer}>
            <PosButton
              id="btnDeleteAllLineItems"
              onClick={() => {
                dispatch(removeAllLineItemsInTab(currentTab));
              }}
              iconElm={DeleteIcon}
              customType="outlined"
              colorType="destruction"
              className={classes.button}
              fontSize={14}
              fontWeight={500}
            >
              Xa tt c sp (
              {getHotkeyDisplayName(hotkey.order.deleteAllLineItems)})
            </PosButton>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

const mapStateToProps = (state: AppState) => ({
  currentTab: state.currentTab,
  hotkey: state.hotkey,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default connector(ActionPanel);
