import { Box, IconButton, Button } from "@material-ui/core";
import clsx from "clsx";
import { memo, useState } from "react";
import { connect, ConnectedProps } from "react-redux";
import { CloseIcon } from "../../../../../../../../components/SVG";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../../../components/typography/PosTypography/PosTypography";
import { AppState } from "../../../../../../../../store";
import useStyles from "./Tab.style";

export interface PosTabProps extends PropsFromRedux {
  active?: boolean;
  onClick?: () => void;
  onRemove?: () => void;
  lable?: string;
}

const Tab = (props: PosTabProps) => {
  const { onClick, lable, active, onRemove } = props;
  const classes = useStyles(props)();
  return (
    <Box
      onClick={onClick}
      className={clsx(classes.root, classes.activeTab)}
      data-active-tab={active}
    >
      <b className={classes.leftCurve}></b>
      <b className={classes.rightCurve}></b>
      <PosTypography
        size={TEXTSIZE.size14}
        className={classes.textStyle}
        fontWeight={active ? "medium" : "regular"}
      >
        {lable}
      </PosTypography>
      <Box
        className={classes.closeIcon}
        onClick={(e) => {
          e.stopPropagation();
          if (onRemove) onRemove();
        }}
        display={"flex"}
        alignItems="center"
        data-close
      >
        <CloseIcon width={16} height={16} style={{ width: 16, height: 16 }} />
      </Box>
    </Box>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(Tab));
