import axios from "axios";
import { ProductFilterModel } from "./models/ProductFilterModels";
import { ProductResponse } from "./models/ProductResponse";

export default class VariantApiService {
  static async getProducts(
    filter: ProductFilterModel
  ): Promise<ProductResponse[]> {
    const response = await axios.get<{ products: ProductResponse[] }>(
      `${process.env.REACT_APP_API_BASE_URL}/admin/products.json`,
      {
        params: filter,
      }
    );
    return response.data.products;
  }
}
