import { makeStyles, Switch, SwitchProps } from "@material-ui/core";
import { memo } from "react";
import { connect, ConnectedProps } from "react-redux";
import { AppState } from "../../store";
import { useStyles } from "./PosToggle.style";

export interface PosToggleProps extends PropsFromRedux, SwitchProps {}

const PosToggle = (props: PosToggleProps) => {
  const classes = useStyles(props)();
  return <Switch {...props} className={classes.root} />;
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(PosToggle));
