import { makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) => {
  return {
    root: {
      position: "absolute",
      top: 44,
      width: "100%",
      backgroundColor: theme.palette.customColors.background.white,
      padding: 0,
      borderRadius: 8,
      border: `solid 1px ${theme.palette.customColors.ink[10]}`,
    },
    addVariant: {
      padding: 12,
    },
    variant: {
      padding: "8px 12px",
      gap: 12,
      width: "100%",
      position: "relative",
      display: "flex",

      "&:before": {
        content: '""',
        width: "calc(100% - 24px)",
        borderTop: `solid 1px ${theme.palette.customColors.ink[10]}`,
        margin: 0,
        position: "absolute",
        top: 0,
      },
    },
    variantImage: {
      width: 52,
      height: 52,
      borderRadius: 8,
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      background: "#fff",
      border: `solid 1px ${theme.palette.customColors.ink[10]}`,
      "& img": {
        maxWidth: 45,
        maxHeight: 45,
      },
    },
    variantName: {
      flex: "1 1 0",
      minWidth: 0,
    },
    variantPrice: {
      textAlign: "right",
    },
    sku: {
      color: theme.palette.customColors.ink[50],
    },
    price: {
      color: theme.palette.customColors.primary[100],
    },
    quantity: {
      color: theme.palette.customColors.ink[70],
      fontSize: 14,
      lineHeight: 2,
    },
  };
});

export default useStyles;
