import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { TenantContext } from "./types";

const initialState: TenantContext = {
  test: "TEST",
};

export const fetchTenantContext = createAsyncThunk(
  "tenantContext/fetchTenantContext",
  async (params, thunkAPI) => {
    // todo: ly info tenant  y
    try {
      const context: TenantContext = {
        test: "haha " + new Date().toISOString(),
      };
      return Promise.resolve(context);
    } catch (error) {
      return Promise.reject();
    }
  }
);

const tenantContextSlice = createSlice({
  name: "tenantContext",
  initialState: initialState,
  reducers: {
    initTenant: (state, action: PayloadAction<Date>) => {
      state.tenantInitAt = action.payload.toISOString();
    },
  },
  extraReducers: (builder) => {
    builder.addCase(
      `${fetchTenantContext.fulfilled}`,
      (state, action: PayloadAction<TenantContext>) => {
        state.test = action.payload.test;
      }
    );
  },
});

const { actions, reducer } = tenantContextSlice;
export const { initTenant } = actions;
const tenantContextReducer = reducer;
export default tenantContextReducer;
