import { useEffect } from "react";
import "./App.css";
import {
  fetchTenantContext,
  initTenant,
} from "./global-slices/tenant-context/slice";
import { useDispatch } from "react-redux";
import { RouterProvider } from "react-router-dom";
import router from "./Routes";
import { SnackbarProvider } from "notistack";
import { SnackbarUtilsConfigurator } from "./utils/Snackbar/SnackbarUtilsConfigurator";

function App() {
  const dispatch = useDispatch<any>();

  useEffect(() => {
    dispatch(fetchTenantContext());
    dispatch(initTenant(new Date()));
  }, [dispatch]);

  return (
    <SnackbarProvider maxSnack={1} hideIconVariant={true}>
      <SnackbarUtilsConfigurator />
      <RouterProvider router={router} />;
    </SnackbarProvider>
  );
}

export default App;
