import { SvgIconProps } from "@material-ui/core";
import { useSelector } from "react-redux";
import { AppState } from "../../store";

interface PosSvgIconProps extends SvgIconProps {
  iconColor?: string;
}

function PlusCircleThickIcon(props: PosSvgIconProps) {
  const { iconColor, htmlColor, ...customProps } = props;
  const theme = useSelector((state: AppState) => state.theme);

  return (
    <svg
      {...customProps}
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <rect
        width="24"
        height="24"
        rx="12"
        fill={htmlColor ?? props.fill ?? theme.current.color.icon.primary}
      />
      <path
        d="M7.5 12H16.5M12 7.5V16.5"
        stroke={iconColor ?? theme.current.color.icon.secondary}
        strokeWidth="1.5"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
}

export default PlusCircleThickIcon;
