import React, { ReactNode, useState } from "react";
import ToggleButton, { ToggleButtonProps } from "@material-ui/lab/ToggleButton";
import clsx from "clsx";
import useStyles from "./PaymentToggleButton.style";
import CashGraphic from "../../../../../../components/Graphics/CashGraphic";
import { Box } from "@material-ui/core";
import {
  CancelCircleIcon,
  CheckedCircleIcon,
} from "../../../../../../components/SVG";
import colors from "../../../../../../theme/statics/colors";
import CheckedIcon from "../../../../../../components/SVG/CheckedIcon";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../components/typography/PosTypography/PosTypography";

export interface PosToggleButtonProps extends ToggleButtonProps {
  icon?: ReactNode;
  isExclusive?: boolean;
  label?: string;
  isSelected?: boolean;
  isFocus?: boolean;

}

const PaymentToggleButton = (props: PosToggleButtonProps) => {
  const classes = useStyles(props)();
  const { icon, className, isExclusive, label, isSelected, isFocus } = props;
  const [isFocusse, setIsFocusse] = useState<boolean>(false);
  return (
    <Box className={classes.root} data-payment-method-focus={isFocusse} data-selected-payment-method={!!isSelected}>
      <ToggleButton
        disableFocusRipple
        {...props}
        className={clsx(className, classes.toggleWrap)}
        data-payment-method-button
        onFocus={() => {
          setIsFocusse(true)
        }}
        onBlur={() => {
          setIsFocusse(false)
        }}
      >
        {icon ?? <CashGraphic />}
      </ToggleButton>
      {!isExclusive && (
        <Box className={classes.iconWrap}>
          <CheckedCircleIcon htmlColor={colors.primary[100]} />
        </Box>
      )}
      <PosTypography
        className={classes.label}
        size={TEXTSIZE.size14}
        textColor={colors.text[90]}
      >
        {label}
      </PosTypography>
    </Box>
  );
};

export default PaymentToggleButton;
