import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { v4 } from "uuid";
import { OrderLineItemModel } from "../../../../models/OrderLineItemModel";
import { Variant } from "../../../../models/Variant";
import { removeTab } from "../tabs/slice";

const initialState: Record<string, OrderLineItemModel[]> = {};

const slice = createSlice({
  name: "orderLineItems",
  initialState: initialState,
  reducers: {
    addLineItem: (
      state,
      action: PayloadAction<{
        tabId: string;
        variant: Variant;
        quantity?: number;
        separateLine?: boolean;
      }>
    ) => {
      const { variant, tabId, quantity, separateLine } = action.payload;
      if (separateLine) {
        addNewLine(state, action);
      } else {
        const currentLine = state[tabId]?.find(
          (x) => x.variant?.id === variant.id
        );
        if (currentLine) {
          incrementCurrentLine(currentLine, quantity);
        } else {
          addNewLine(state, action);
        }
      }
    },
    addServiceLineItem: (
      state,
      action: PayloadAction<{
        tabId: string;
        quantity?: number;
      }>
    ) => {
      const newAction: PayloadAction<{
        tabId: string;
        variant?: Variant;
        quantity?: number;
        separateLine?: boolean;
        isFreeForm?: boolean;
      }> = {
        payload: {
          tabId: action.payload.tabId,
          quantity: action.payload.quantity,
          isFreeForm: true,
        },
        type: action.type,
      };
      addNewLine(state, newAction);
    },
    updateLineItemQuantity: (
      state,
      action: PayloadAction<{
        tabId: string;
        lineItemId: string;
        quantity: number;
      }>
    ) => {
      const { tabId, lineItemId, quantity } = action.payload;
      const lineItem = state[tabId]?.find((li) => li.id === lineItemId);
      if (lineItem) {
        lineItem.quantity = quantity > 0 ? quantity : 0;
        state[tabId] = state[tabId].map((li) =>
          li.id !== lineItemId ? li : lineItem
        );
      }
    },
    updateLineItemUnitPrice: (
      state,
      action: PayloadAction<{
        tabId: string;
        lineItemId: string;
        price: number;
      }>
    ) => {
      const { tabId, lineItemId, price } = action.payload;
      const lineItem = state[tabId]?.find((li) => li.id === lineItemId);
      if (lineItem) {
        lineItem.unitPrice = price > 0 ? price : 0;
        state[tabId] = state[tabId].map((li) =>
          li.id !== lineItemId ? li : lineItem
        );
      }
    },
    removeLineItem: (
      state,
      action: PayloadAction<{
        tabId: string;
        lineItemId: string;
      }>
    ) => {
      const { tabId, lineItemId } = action.payload;
      if (state[tabId]?.some((li) => li.id === lineItemId)) {
        state[tabId] = state[tabId].filter((li) => li.id !== lineItemId);
      }
    },
    removeAllLineItemsInTab: (state, action: PayloadAction<string>) => {
      delete state[action.payload];
      return state;
    },
    changeLineItemCustomName: (
      state,
      action: PayloadAction<{
        tabId: string;
        lineItemId: string;
        name: string;
      }>
    ) => {
      const { tabId, lineItemId, name } = action.payload;
      const lineItem = state[tabId]?.find((li) => li.id === lineItemId);
      if (lineItem) {
        lineItem.customName = name;
        state[tabId] = state[tabId].map((li) =>
          li.id !== lineItemId ? li : lineItem
        );
      }
    },
    changeLineItemNote: (
      state,
      action: PayloadAction<{
        tabId: string;
        lineItemId: string;
        note: string;
      }>
    ) => {
      const { tabId, lineItemId, note } = action.payload;
      const lineItem = state[tabId]?.find((li) => li.id === lineItemId);
      if (lineItem) {
        lineItem.note = note;
        state[tabId] = state[tabId].map((li) =>
          li.id !== lineItemId ? li : lineItem
        );
      }
    },
  },
  extraReducers: (builder) => {
    builder.addCase(`${removeTab}`, (state, action: PayloadAction<string>) => {
      slice.caseReducers.removeAllLineItemsInTab(state, action);
    });
  },
});

const addNewLine = (
  state: Record<string, OrderLineItemModel[]>,
  action: PayloadAction<{
    tabId: string;
    variant?: Variant;
    quantity?: number;
    separateLine?: boolean;
    isFreeForm?: boolean;
  }>
) => {
  const { variant, tabId, quantity, isFreeForm } = action.payload;
  const orderLineItem: OrderLineItemModel = {
    id: v4(),
    variant: variant,
    quantity: quantity ?? 1,
    isFreeForm: isFreeForm,
    unitPrice: variant?.price ?? 0,
  };
  if (!state[tabId]) {
    state[tabId] = [orderLineItem];
  } else {
    state[tabId].push(orderLineItem);
  }
};

const incrementCurrentLine = (
  currentLine: OrderLineItemModel,
  quantity?: number
) => {
  currentLine.quantity += quantity ?? 1;
};

const { actions, reducer } = slice;
export const {
  addLineItem,
  updateLineItemQuantity,
  removeLineItem,
  updateLineItemUnitPrice,
  removeAllLineItemsInTab,
  addServiceLineItem,
  changeLineItemCustomName,
  changeLineItemNote,
} = actions;
const orderLineItemsReducer = reducer;
export default orderLineItemsReducer;
