import { TypedUseSelectorHook, useSelector } from "react-redux";
import { AppState } from "../../../../store";
import {
  activateNextTab,
  activatePreviousTab,
  clickAddOrderTabButton,
  clickAddServiceLineItem,
  clickCreateOrderButton,
  clickDeleteAllLineItems,
  clickTabDeleteButton,
  openCustomerSearchBar,
  openVariantSearchBar,
} from "./operations";

const useTypedSelector: TypedUseSelectorHook<AppState> = useSelector;

export const useHotkeys = () => {
  const hotkeyConfig = useTypedSelector((state) => state.hotkey);

  const registerOrderEventListeners = () => {
    document.removeEventListener("keydown", keydownEventHandler);
    document.addEventListener("keydown", keydownEventHandler);
  };

  const keydownEventHandler = (evt: KeyboardEvent) => {
    console.log("xx keydown global", evt.code);
    if (isOpeningVariantSearchBar(evt)) {
      evt.preventDefault();
      openVariantSearchBar();
    } else if (isOpeningCustomerSearchBar(evt)) {
      evt.preventDefault();
      openCustomerSearchBar();
    } else if (isRemovingOrder(evt)) {
      evt.preventDefault();
      clickTabDeleteButton();
    } else if (isAddingOrderTab(evt)) {
      evt.preventDefault();
      clickAddOrderTabButton();
    } else if (isMovingToNextOrderTab(evt)) {
      evt.preventDefault();
      activateNextTab();
    } else if (isMovingToPreviousTab(evt)) {
      evt.preventDefault();
      activatePreviousTab();
    } else if (isCreatingOrder(evt)) {
      evt.preventDefault();
      clickCreateOrderButton();
    } else if (isDeletingAllLineItems(evt)) {
      evt.preventDefault();
      clickDeleteAllLineItems();
    } else if (isAddingServiceLineItem(evt)) {
      evt.preventDefault();
      clickAddServiceLineItem();
    }
  };

  const isOpeningVariantSearchBar = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.openVariantSearchBar;

  const isOpeningCustomerSearchBar = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.openCustomerSearchBar;

  const isRemovingOrder = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.deleteOrder;

  const isAddingOrderTab = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.addOrderTab;

  const isMovingToNextOrderTab = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.moveToNextTab;

  const isMovingToPreviousTab = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.moveToPreviousTab;

  const isCreatingOrder = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.createOrder;

  const isDeletingAllLineItems = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.deleteAllLineItems;

  const isAddingServiceLineItem = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.addService;

  return {
    registerOrderEventListeners,
  };
};

export default useHotkeys;
