import { createSlice, PayloadAction } from "@reduxjs/toolkit";

const initialState: string = "";

const currentTabSlice = createSlice({
  name: "currentTab",
  initialState: initialState,
  reducers: {
    updateCurrentTab: (state, action: PayloadAction<string>) => {
      state = action.payload;
      return state;
    },
  },
});

const { actions, reducer } = currentTabSlice;
export const { updateCurrentTab } = actions;
const currentTabReducer = reducer;
export default currentTabReducer;
