export function formatPrice(money: number) {
  var value = money.toFixed(1).replace(/\d(?=(\d{3})+\.)/g, "$&,");
  return value.substring(0, value.length - 2);
}

export function formatPriceWithVNDCurrency(money: number) {
  var value = money.toFixed(1).replace(/\d(?=(\d{3})+\.)/g, "$&,");
  return value.substring(0, value.length - 2) + " ";
}
