import { makeStyles } from "@material-ui/core";
import colors from "../../theme/statics/colors";
import { NumberInputTextFieldProps } from "./PosNumberInput";

export const useStyles = (props: NumberInputTextFieldProps) => {
  const primayColor =
    props.theme.current.color.primary[100] ?? colors.primary[100];
  const borderColor = props.theme.current.color.ink[10] ?? colors.ink[10];
  const disabledColor = props.theme.current.color.text[50] ?? colors.text[50];
  const textColor = props.theme.current.color.text[100] ?? colors.ink[100];
  const selectColor = props.theme.current.color.ink[0] ?? colors.ink[0];
  const disabled = props.disabled;

  return makeStyles((theme) => {
    const defautInput = {
      color: !disabled ? textColor : disabledColor,
      caretColor: primayColor,
      "& .MuiInputBase-input": {
        fontSize: props.fontsize ?? 14,
        fontWeight: props.fontWeight ?? 400,
      },
      "& ::selection": {
        color: selectColor,
        background: primayColor,
      },
      "& .MuiInput-underline:before": {
        borderBottom: `1px solid ${borderColor}  !important`,
      },
      "& .MuiInput-underline:after": {
        borderBottom: `1px solid ${primayColor}  !important`,
      },
    };
    return {
      defautInput,
    };
  });
};
