import {
  Button,
  ClickAwayListener,
  Grow,
  MenuItem,
  MenuList,
  Paper,
  Popper,
} from "@material-ui/core";
import { memo, useRef, useState } from "react";
import { connect, ConnectedProps } from "react-redux";
import {
  ArrowDropDownIcon,
  BarcodeIcon,
} from "../../../../../../../../components/SVG";
import { AppState } from "../../../../../../../../store";
import useStyles from "./UtilDropdown.styles";

const UtilDropdown = (props: PropsFromRedux) => {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const anchorRef = useRef<HTMLButtonElement>(null);

  const handleClose = (event: React.MouseEvent<EventTarget>) => {
    if (
      anchorRef.current &&
      anchorRef.current.contains(event.target as HTMLElement)
    ) {
      return;
    }

    setOpen(false);
  };

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  function handleListKeyDown(event: React.KeyboardEvent) {
    if (event.key === "Tab") {
      event.preventDefault();
      setOpen(false);
    }
  }

  return (
    <>
      <Button
        ref={anchorRef}
        aria-controls={open ? "variant-search-box-util-menu" : undefined}
        aria-haspopup="true"
        onClick={handleToggle}
        disableRipple={true}
        className={classes.root}
      >
        <BarcodeIcon />
        <ArrowDropDownIcon
          fill={props.theme.current.color.icon.primary}
          className={classes.utilDropdownIcon}
        />
      </Button>
      <Popper
        open={open}
        anchorEl={anchorRef.current}
        role={undefined}
        transition
        disablePortal
      >
        {({ TransitionProps, placement }) => (
          <Grow
            {...TransitionProps}
            style={{
              transformOrigin:
                placement === "bottom" ? "center top" : "center bottom",
            }}
          >
            <Paper>
              <ClickAwayListener onClickAway={handleClose}>
                <MenuList
                  autoFocusItem={open}
                  id="variant-search-box-util-menu"
                  onKeyDown={handleListKeyDown}
                  style={{
                    fontSize: 14,
                  }}
                >
                  <MenuItem onClick={handleClose}>Qut m barcode</MenuItem>
                </MenuList>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
    </>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(UtilDropdown));
