import { makeStyles } from "@material-ui/core";
import colors from "../../theme/statics/colors";
import { PosToggleProps } from "./PosToggle";

export const useStyles = (props: PosToggleProps) => {
  const thumb = props.theme.current.color.toggle.thumb;
  const checkedTrack =
    props.theme.current.color.toggle.checkedTrack ?? colors.primary[100];
  const unCheckedTrack =
    props.theme.current.color.toggle.unCheckedTrack ?? colors.ink[50];
  const disableTrack =
    props.theme.current.color.toggle.disableTrack ?? colors.ink[10];

  return makeStyles((theme) => {
    const root = {
      width: 38,
      height: 20,
      padding: 0,
      "& .MuiSwitch-switchBase": {
        padding: 0,
        margin: "2px",
        transitionDuration: "300ms",
        "&.Mui-checked": {
          transform: "translateX(18px)",
          color: thumb,
          "& + .MuiSwitch-track": {
            backgroundColor: checkedTrack,
            opacity: 1,
            border: 0,
          },
        },
        "&.Mui-disabled .MuiSwitch-thumb": {
          color: thumb,
        },
        "&.Mui-disabled .MuiSwitch-track": {
          opacity: "1 !important",
          color: thumb,
        },
      },
      "& .MuiSwitch-thumb": {
        boxSizing: "border-box",
        width: 16,
        height: 16,
      },
      "& .MuiSwitch-track": {
        borderRadius: 20 / 2,
        backgroundColor: unCheckedTrack,
        opacity: "1 !important",
        transition: theme.transitions.create(["background-color"], {
          duration: 500,
        }),
      },
      "& .MuiSwitch-colorSecondary.Mui-disabled + .MuiSwitch-track": {
        backgroundColor: disableTrack,
      },
    };
    return {
      root,
    };
  });
};
