import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { OrderTabStateModel } from "./types";
import { initTenant } from "../../../../global-slices/tenant-context/slice";
import { v4 as uuidv4 } from "uuid";

const initialState: OrderTabStateModel = {
  tabs: [],
  nextIndex: 0,
};

const tabListSlice = createSlice({
  name: "tabList",
  initialState: initialState,
  reducers: {
    addTab: (state) => {
      state.tabs.push({
        tabId: uuidv4(),
        tabIndex: state.nextIndex,
      });
      state.nextIndex = Math.max(...state.tabs.map(t => t.tabIndex)) + 1
    },
    removeTab: (state, action: PayloadAction<string>) => {
      state.tabs = state.tabs.filter((t) => t.tabId !== action.payload);
      if (state.tabs.length === 0) {
        state.nextIndex = 0; 
      } else {
        state.nextIndex = Math.max(...state.tabs.map(t => t.tabIndex)) +1
      }
    },
  },
  extraReducers: (builder) => {
    builder.addCase(`${initTenant}`, (state, action: PayloadAction<string>) => {
      if (state.tabs.length === 0) {
        tabListSlice.caseReducers.addTab(state);
      }
    });
  },
});

const { actions, reducer } = tabListSlice;
export const { addTab, removeTab } = actions;
const tabListReducer = reducer;
export default tabListReducer;
