import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { Provider } from "react-redux";
import storeProvider from "./store";
import { PersistGate } from "redux-persist/integration/react";
import { ThemeProvider } from "@material-ui/core";
import { createAppTheme } from "./theme";
import { initInterceptors } from "./external-services/api/interceptors";

initInterceptors();

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);

const Root = () => {
  const theme = createAppTheme(storeProvider.store.getState().theme);

  return (
    <ThemeProvider theme={theme}>
      <Provider store={storeProvider.store}>
        <PersistGate loading={null} persistor={storeProvider.persistor}>
          <App />
        </PersistGate>
      </Provider>
    </ThemeProvider>
  );
};

root.render(<Root />);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
