import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { CustomerResponse } from "../../../../external-services/api/models/CustomerModels";

const initialState: Record<string, CustomerResponse> = {};

const tenantContextSlice = createSlice({
  name: "customer",
  initialState: initialState,
  reducers: {
    setCustomer: (
      state: Record<string, CustomerResponse>,
      action: PayloadAction<{
        tabId: string;
        customer: CustomerResponse;
      }>
    ) => {
      const { tabId, customer } = action.payload;
      setNewCustomer(state, action);
    },
    removeCustomer: (
      state,
      action: PayloadAction<{
        tabId: string;
      }>
    ) => {
      delete state[action.payload.tabId];
      return state;
    },
  },
});

const setNewCustomer = (
  state: Record<string, CustomerResponse>,
  action: PayloadAction<{
    tabId: string;
    customer: CustomerResponse;
  }>
) => {
  const { tabId, customer } = action.payload;
  const currentCustomer: CustomerResponse = customer;
  state[tabId] = currentCustomer;
};

const { actions, reducer } = tenantContextSlice;
export const { setCustomer, removeCustomer } = actions;
const customerReducer = reducer;
export default customerReducer;
