import { Box, debounce, Grid } from "@material-ui/core";
import { memo, useEffect } from "react";
import { connect, ConnectedProps, useDispatch } from "react-redux";
import EmptyProductGraphic from "../../../../components/Graphics/EmptyProductGraphic";
import { AppState } from "../../../../store";
import { updateLineItemQuantity } from "../../slices/order-line-items/slice";
import useStyles, {
  columnLeftPaddings,
  columnWidths,
} from "./BoxLineItems.styles";
import ActionPanel from "./components/ActionPanel/ActionPanel";
import OrderFooter from "./components/OrderFooter/OrderFooter";
import OrderLineItem from "./components/OrderLineItem/OrderLineItem";

const BoxLineItems = (props: PropsFromRedux) => {
  const classes = useStyles();
  const { orderLineItems, theme, hotkey, currentTab, customer } = props;
  const dispatch = useDispatch<any>();

  useEffect(() => {
    if (orderLineItems?.length > 0) {
      setTimeout(focusOnFirstLineItem, 0);
    }
  }, [orderLineItems?.length, customer]);

  const focusOnFirstLineItem = () => {
    let optionElm = document.querySelector(
      "[data-order-line-item]"
    ) as HTMLElement;
    optionElm?.focus();
  };

  const isChangingQuantity = (e: KeyboardEvent) => {
    return (
      hotkey.order.lineItem.changeQuantityKeyType === "numpad" &&
      /^Numpad[0-9]/.test(e.code)
    );
  };

  // x l bm s khi focus vo line-item
  useEffect(() => {
    if (
      !orderLineItems ||
      orderLineItems.length === 0 ||
      !currentTab ||
      currentTab == ""
    )
      return;
    let inputTemp = "";
    const debounceObj = debounce(() => {
      inputTemp = "";
    }, 1000);

    const handleQuantityChange = (event: KeyboardEvent) => {
      if (
        document.activeElement === event.target &&
        isChangingQuantity(event)
      ) {
        if (inputTemp) {
          inputTemp += event.key;
        } else {
          inputTemp = event.key;
        }
        debounceObj.clear();
        debounceObj();
        dispatch(
          updateLineItemQuantity({
            tabId: currentTab,
            lineItemId: document.activeElement?.id.substring(9) || "", // todo: sa ch ny
            quantity: Number.parseInt(inputTemp),
          })
        );
      } else {
        inputTemp = "";
        debounceObj.clear();
        debounceObj();
      }
    };

    document
      .getElementById("orderLineItemList")
      ?.addEventListener("keyup", handleQuantityChange);
    return () => {
      document
        .getElementById("orderLineItemList")
        ?.removeEventListener("keyup", handleQuantityChange);
    };
  }, [orderLineItems?.length, currentTab]);

  const renderHeading = () => (
    <Grid className={classes.lineItemListHeading}>
      <Box flex="1 1 0" paddingLeft="16px">
        Sn phm (
        <b
          style={{
            color: theme.current.color.text[100],
          }}
        >
          {orderLineItems?.length ?? 0}
        </b>
        )
      </Box>
      <Box
        width={columnWidths.unit}
        minWidth={columnWidths.unit}
        textAlign="right"
      >
        n v
      </Box>
      <Box
        width={columnWidths.unitPrice}
        minWidth={columnWidths.unitPrice}
        textAlign="right"
        paddingLeft={`${columnLeftPaddings.unitPrice}px`}
      >
        n gi
      </Box>
      <Box
        width={columnWidths.quantity}
        minWidth={columnWidths.quantity}
        textAlign="center"
        paddingLeft={`${columnLeftPaddings.quantity}px`}
      >
        S lng
      </Box>
      <Box
        width={columnWidths.toAmount}
        minWidth={columnWidths.toAmount}
        textAlign="right"
      >
        Thnh tin
      </Box>
      <Box width={columnWidths.delete}></Box>
    </Grid>
  );

  return (
    <Box className={classes.root}>
      <Box position="relative" flex="1 1 0" overflow="auto">
        {renderHeading()}
        <Box id="orderLineItemList">
          {orderLineItems?.length > 0 ? (
            orderLineItems
              .slice()
              .reverse()
              .map((lineItem) => (
                <OrderLineItem key={lineItem.id} lineItem={lineItem} />
              ))
          ) : (
            <Box className={classes.lineItemsEmpty}>
              <EmptyProductGraphic />
            </Box>
          )}
        </Box>
      </Box>
      <ActionPanel />
      <OrderFooter />
    </Box>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
  currentTab: state.currentTab,
  orderLineItems: state.orderLineItems[state.currentTab],
  hotkey: state.hotkey,
  customer: state.customer[state.currentTab],
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(BoxLineItems));
