import React, { ReactNode } from "react";
import { Box, MenuItem, Select, SelectProps } from "@material-ui/core";
import { AppState } from "../../store";
import { connect, ConnectedProps } from "react-redux";
import { useStyles } from "./Dropdown.style";
import { ArrowDropDownIcon } from "../SVG";
import colors from "../../theme/statics/colors";
export interface DropdownProps extends SelectProps {
  options?: any[];
  width?: number;
  textLabel?: string;
  lableIcon?: ReactNode;
  size?: string;
  renderValue?: (value: unknown) => React.ReactNode;
  renderOption: (option: any) => React.ReactNode;
  styles?: {
    box?: React.CSSProperties;
  };
}

const Dropdown = (props: DropdownProps) => {
  const classes = useStyles(props)();
  const { options, renderOption, value, styles, ...selectProps } = props;
  const customProps = {
    ...selectProps,
  } as SelectProps;

  const dropIcon = (props: any) => {
    return <ArrowDropDownIcon {...props} htmlColor={colors.icon.secondary} />;
  };

  return (
    <Box className={classes.selectBox} style={styles?.box}>
      <Select
        {...customProps}
        value={value}
        renderValue={(value) =>
          props.renderValue ? (
            props.renderValue(value)
          ) : (
            <div>{value as string}</div>
          )
        }
        style={{
          width: "100%",
        }}
        MenuProps={{
          classes: { paper: classes.paper },
          anchorOrigin: {
            vertical: "bottom",
            horizontal: "left",
          },
          transformOrigin: {
            vertical: "top",
            horizontal: "left",
          },
          getContentAnchorEl: null,
        }}
        IconComponent={dropIcon}
        classes={{
          icon: classes.iconSelect,
          select: classes.root,
        }}
      >
        {options &&
          options.map((item, index) => (
            // c thi gian s sa li, b sc mnh php thut ch ny i
            <MenuItem
              //@ts-ignore
              value={item}
              key={index}
              className={
                JSON.stringify(item) === JSON.stringify(value)
                  ? classes.menuItemChossen
                  : classes.menuItem
              }
            >
              <Box className={classes.boxItem}>{renderOption(item)}</Box>
            </MenuItem>
          ))}
      </Select>
    </Box>
  );
};

export default Dropdown;
