export const openVariantSearchBar = () => {
  const inputElm = document.getElementById("variantSearch");
  inputElm?.blur();
  inputElm?.focus();
};

export const openCustomerSearchBar = () => {
  const inputElm = document.getElementById("customerSearch");
  inputElm?.blur();
  inputElm?.focus();
};

export const clickTabDeleteButton = () => {
  (
    document.querySelector(
      "#tabList [data-active-tab='true'] [data-close]"
    ) as HTMLElement
  )?.click();
};

export const clickAddOrderTabButton = () => {
  (document.querySelector("#btnAddOrderTab") as HTMLElement)?.click();
};

export const activateNextTab = () => {
  const nextSibling = document.querySelector(
    "#tabList [data-active-tab='true']"
  )?.nextElementSibling as HTMLElement;
  if (nextSibling) {
    nextSibling.click();
  } else {
    const firstChild = document.getElementById("tabList")
      ?.firstElementChild as HTMLElement;
    firstChild?.click();
  }
};

export const activatePreviousTab = () => {
  const prevSibling = document.querySelector(
    "#tabList [data-active-tab='true']"
  )?.previousElementSibling as HTMLElement;
  if (prevSibling) {
    prevSibling.click();
  } else {
    const lastChild = document.getElementById("tabList")
      ?.lastElementChild as HTMLElement;
    lastChild?.click();
  }
};

export const clickCreateOrderButton = () => {
  document.getElementById("btnPayForOrder")?.click();
};

export const clickDeleteAllLineItems = () => {
  document.getElementById("btnDeleteAllLineItems")?.click();
};

export const clickAddServiceLineItem = () => {
  document.getElementById("btnAddService")?.click();
};
