import { TextField } from "@material-ui/core";
import { memo, useEffect, useState } from "react";
import { connect, ConnectedProps, useDispatch } from "react-redux";
import { PlusCircleIcon, SearchIcon } from "../../../../../../components/SVG";
import { VariantResponse } from "../../../../../../external-services/api/models/VariantResponse";
import VariantApiServices from "../../../../../../external-services/api/VariantApiServices";
import { AppState } from "../../../../../../store";
import useStyles from "./ProductSearch.styles";
import UtilDropdown from "./components/UtilDropdown";
import { debounce } from "lodash";
import Autocomplete from "@material-ui/lab/Autocomplete";
import VariantOption from "./components/VariantOption/VariantOption";
import { VariantSearchOption } from "./types";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../components/typography/PosTypography/PosTypography";
import { DefaultLoadingIcon } from "../../../../../../components/utils";
import { addLineItem } from "../../../../slices/order-line-items/slice";
import { ProductResponse } from "../../../../../../external-services/api/models/ProductResponse";
import {
  convertToVariantModel,
  getVariantFullName,
  getVariantImages,
} from "../../../../../../helpers/variantHelpers";

const ProductSearch = (props: PropsFromRedux) => {
  const { currentTab, hotkey } = props;
  const classes = useStyles();
  const dispatch = useDispatch<any>();

  const [options, setOptions] = useState<VariantSearchOption[]>([]);
  const [open, setOpen] = useState<boolean>(false);
  const [keyword, setKeyword] = useState<string | undefined>();
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    if (!open) {
      setOptions([]);
      return;
    }
    searchVariants();
  }, [open, keyword]);

  useEffect(() => {
    setTimeout(hoverOnFirstOption, 0);
  }, [options]);

  const hoverOnFirstOption = () => {
    let optionElm = document.querySelector("[data-variant-option]");
    if (!optionElm) {
      optionElm = document.querySelector("[data-variant-add-option]");
    }
    if (optionElm) {
      var event = new MouseEvent("mouseover", {
        view: window,
        bubbles: true,
        cancelable: true,
      });
      optionElm.parentElement?.firstElementChild?.dispatchEvent(event);
    }
  };

  const handleSearchInputChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    event.stopPropagation();
    event.preventDefault();
    debounceSetKeyword(event.target.value);
  };

  const debounceSetKeyword = debounce((keyword) => {
    setKeyword(keyword);
  }, 500);

  const searchVariants = async () => {
    console.log("searching variant. kw:", keyword);
    setLoading(true);
    const products = await VariantApiServices.getProducts({
      query: keyword,
      limit: 20,
    });
    setOptions(generateOptions(products));
    setLoading(false);
  };

  const generateOptions = (
    products: ProductResponse[]
  ): VariantSearchOption[] => {
    const options: VariantSearchOption[] = [];
    options.push({
      type: "addVariant",
    });
    if (products.length > 0) {
      products.forEach((p) => {
        p.variants.forEach((v) => {
          v.name = getVariantFullName(v, p);
          v.images = getVariantImages(v, p);
          options.push({
            variant: v,
            type: "variant",
          });
        });
      });
    } else {
      options.push({
        type: "noOptions",
      });
    }
    return options;
  };

  const handleSelectVariant = (variant: VariantResponse) => {
    const variantModel = convertToVariantModel(variant);
    console.log(variantModel);
    dispatch(
      addLineItem({
        tabId: currentTab,
        variant: convertToVariantModel(variant),
        separateLine: false,
      })
    );
  };

  const renderAddOption = () => (
    <div className={classes.addVariant} data-variant-add-option>
      <PlusCircleIcon
        fill={props.theme.current.color.primary[100]}
        style={{
          marginRight: 8,
        }}
      />
      <PosTypography
        size={TEXTSIZE.size16}
        fontWeight="regular"
        textColor={props.theme.current.color.primary[100]}
      >
        Thm sn phm mi
      </PosTypography>
    </div>
  );

  const renderNoOptions = () => (
    <div className={classes.noVariants}>
      <PosTypography size={TEXTSIZE.size14} fontWeight="regular">
        Khng tm thy sn phm
      </PosTypography>
    </div>
  );

  return (
    <div className={classes.root}>
      <div className={classes.searchInput}>
        <Autocomplete
          id="variantSearch"
          className={classes.searchAuto}
          classes={{
            listbox: classes.listbox,
            paper: classes.paper,
            popper: classes.popper,
            option: classes.option,
          }}
          open={open}
          getOptionSelected={(option) =>
            option.type === "variant" &&
            option.variant?.sku === options[1]?.variant?.sku
          }
          options={options}
          getOptionLabel={() => ""}
          openOnFocus
          selectOnFocus
          disableClearable
          autoComplete={false}
          loading={loading}
          closeIcon={null}
          popupIcon={null}
          loadingText={<DefaultLoadingIcon />}
          // disablePortal
          getOptionDisabled={(option) => option.type === "noOptions"}
          renderInput={(params) => (
            <>
              <SearchIcon />
              <TextField
                {...params}
                placeholder={`Nhp tn sn phm hoc m SKU (${hotkey.order.openVariantSearchBar})`}
                className={classes.input}
                InputProps={{ ...params.InputProps, disableUnderline: true }}
                onChange={handleSearchInputChange}
              />
            </>
          )}
          filterOptions={(x) => x}
          includeInputInList
          renderOption={(option) => {
            if (option.type === "variant")
              return <VariantOption variant={option.variant!} />;
            if (option.type === "addVariant") return renderAddOption();
            return renderNoOptions();
          }}
          onClose={() => {
            setOpen(false);
            setOptions([]);
          }}
          onOpen={() => {
            setOpen(true);
            setKeyword(undefined);
          }}
          onChange={(ev, value) => {
            ev.preventDefault();
            if (value?.variant) {
              handleSelectVariant(value.variant);
            }
          }}
        />
        <UtilDropdown />
      </div>
    </div>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
  currentTab: state.currentTab,
  hotkey: state.hotkey,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(ProductSearch));
