import { Box, IconButton, InputAdornment, TextField } from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import { memo, useState, useEffect } from "react";
import { connect, ConnectedProps, useDispatch } from "react-redux";
import { PlusIcon, UserIcon } from "../../../../../../components/SVG";
import { AppState } from "../../../../../../store";
import useStyles from "./CustomerSearch.style";
import colors from "../../../../../../theme/statics/colors";
import PosTypography from "../../../../../../components/typography/PosTypography/PosTypography";
import CustomerApiService from "../../../../../../external-services/api/CustomerApiServices";
import { CustomerResponse } from "../../../../../../external-services/api/models/CustomerModels";
import { DefaultLoadingIcon } from "../../../../../../components/utils";
import { setCustomer } from "../../../../slices/customer/slice";
import { getCustomerDisplayName } from "../../../../../../helpers/customerHelpers";
import { debounce } from "lodash";

interface CustomerSearchProps extends PropsFromRedux {
  openDialog: () => void;
}

const CustomerSearch = (props: CustomerSearchProps) => {
  const { openDialog } = props;
  const classes = useStyles();
  const disable = false;
  const { currentTab, hotkey } = props;
  const [loading, setLoading] = useState<boolean>(false);
  const [inputValue, setInputValue] = useState<string>("");
  const [open, setOpen] = useState<boolean>(false);
  const [keyword, setKeyword] = useState<string | undefined>();
  const [customers, setCustomers] = useState<CustomerResponse[]>([]);
  const dispatch = useDispatch<any>();

  const searchCustomer = async () => {
    console.log("searching variant. kw:", keyword);
    setLoading(true);
    const customerRes = await CustomerApiService.getCustomers({
      query: keyword,
      sortKey: " created_on",
      reverse: true,
      limit: 20,
    });
    setCustomers(customerRes);
    setLoading(false);
  };

  const handleChange = (e: any) => {
    debounceSetKeyword(e.target.value);
  };

  const debounceSetKeyword = debounce((keyword) => {
    setKeyword(keyword);
  }, 500);

  useEffect(() => {
    setTimeout(hoverOnFirstOption, 0);
  }, [customers]);

  const hoverOnFirstOption = () => {
    let optionElm = document.querySelector("[data-customer-option]");
    if (optionElm) {
      var event = new MouseEvent("mouseover", {
        view: window,
        bubbles: true,
        cancelable: true,
      });
      optionElm.parentElement?.firstElementChild?.dispatchEvent(event);
    }
  };

  useEffect(() => {
    if (!open) {
      setCustomers([]);
      return;
    }
    searchCustomer();
  }, [open, keyword]);
  const handleClickSearchCustomer = () => {};
  const handelSelectCustomer = (customer: CustomerResponse) => {
    dispatch(
      setCustomer({
        tabId: currentTab,
        customer: customer,
      })
    );
  };

  return (
    <Autocomplete
      disabled={disable}
      id="customerSearch"
      disableClearable
      autoComplete={false}
      classes={{
        listbox: classes.listbox,
        option: classes.searchCustomerOption,
        inputRoot: classes.inputRootCustomer,
        noOptions: classes.noOptions,
        loading: classes.loading,
        paper: classes.paperCustomer,
      }}
      noOptionsText={
        keyword !== "" ? "Khng tm thy khch hng" : "Khng c khch hng no"
      }
      loading={loading}
      onClose={() => {
        setInputValue("");
        setOpen(false);
      }}
      openOnFocus
      open={open}
      onOpen={() => {
        setOpen(true);
      }}
      debug={false}
      onChange={(e, value) => {
        e.preventDefault();
        if (value.id) {
          handelSelectCustomer(value);
        }
      }}
      onBlur={() => {
        setKeyword("");
      }}
      filterOptions={(x) => x}
      options={customers}
      getOptionLabel={(option) => ""}
      includeInputInList
      popupIcon={null}
      disablePortal={true}
      closeIcon={null}
      loadingText={<DefaultLoadingIcon />}
      renderInput={(params) => (
        <Box display="flex" position="relative">
          <TextField
            {...params}
            variant="outlined"
            placeholder={`Tm kim khch hng (${hotkey.order.openCustomerSearchBar})`}
            fullWidth
            className={classes.searchInput}
            onChange={handleChange}
            onClick={handleClickSearchCustomer}
            InputProps={{
              ...params.InputProps,
              startAdornment: (
                <InputAdornment position="start">
                  <UserIcon
                    htmlColor={colors.icon.primary}
                    width={24}
                    height={24}
                  />
                </InputAdornment>
              ),
              endAdornment: (
                <IconButton
                  disabled={disable}
                  classes={{ root: classes.btnAddCustomer }}
                  onClick={(e) => {
                    e.stopPropagation();
                    openDialog();
                    // if (isAuthenticate) {
                    //   if (addCustomer) {
                    //     addCustomer(inputValue);
                    //   }
                    // } else {
                    //   enqueueSnackbar(
                    //     t("AutocompleteCustomer.accessDenied"),
                    //     SnackbarErrorProp
                    //   );
                    // }
                  }}
                >
                  {/* <Tooltip title={"Thm khch hng"} placement="bottom-start"> */}
                  <PlusIcon htmlColor={colors.primary[100]} />
                  {/* </Tooltip> */}
                </IconButton>
              ),
            }}
          />
        </Box>
      )}
      renderOption={(option) => {
        return (
          <Box
            borderBottom={`1px solid ${colors.ink[10]}`}
            width={"100%"}
            paddingBottom={"8px"}
            display="flex"
            flexDirection={"column"}
            data-customer-option
          >
            <PosTypography textColor={props.theme.current.color.text[100]}>
              {getCustomerDisplayName(option)}
            </PosTypography>
            <PosTypography textColor={colors.text[70]}>
              {option.phone ?? "- - -"}
            </PosTypography>
          </Box>
        );
      }}
    />
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
  currentTab: state.currentTab,
  hotkey: state.hotkey,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(CustomerSearch));
