import { ImageResponse } from "../external-services/api/models/ImageResponse";
import { ProductResponse } from "../external-services/api/models/ProductResponse";
import { VariantResponse } from "../external-services/api/models/VariantResponse";
import { Variant } from "../models/Variant";

export const getVariantFullName = (
  variant: VariantResponse,
  product: ProductResponse
): string => {
  return (
    product.name +
    (variant.title && variant.title !== "Default Title"
      ? ` - ${variant.title}`
      : "")
  );
};

export const getVariantImages = (
  variant: VariantResponse,
  product: ProductResponse
): ImageResponse[] | undefined => {
  return product.images?.filter((i) => i.variantIds?.includes(variant.id));
};

export const getVariantImageSrc = (
  variant: VariantResponse
): string | undefined => {
  return variant.images?.find((i) => i.variantIds?.includes(variant.id))?.src;
};

export const convertToVariantModel = (
  variantResp: VariantResponse
): Variant => {
  const variant = variantResp as Variant;
  variant.image = getVariantImageSrc(variantResp);
  return variant;
};
