import { Button, ButtonProps, SvgIconProps } from "@material-ui/core";
import { memo } from "react";
import { connect, ConnectedProps } from "react-redux";
import { AppState } from "../../../store";
import useStyles from "./PosButton.styles";

export type PosButtonProps = {
  customType?: "primary" | "outlined" | "text";
  customSize?: "big" | "medium" | "small";
  colorType?: "default" | "destruction";
  iconElm?: React.ComponentType<SvgIconProps>;
  iconPosition?: "left" | "right";
  fontSize?: number;
  fontWeight?: number | string;
} & PropsFromRedux &
  ButtonProps;

const PosButton = (props: PosButtonProps) => {
  const classes = useStyles(props)();

  const {
    customType,
    customSize,
    colorType,
    iconElm,
    iconPosition,
    fontSize,
    fontWeight,
    ...buttonProps
  } = props;
  const customProps: ButtonProps = {
    ...buttonProps,
    disableRipple: true,
  };
  delete (customProps as any).customType;
  delete (customProps as any).customSize;
  delete (customProps as any).colorType;
  delete (customProps as any).iconElm;
  delete (customProps as any).iconPosition;
  delete (customProps as any).dispatch;

  return (
    <Button
      {...customProps}
      className={`${classes.root} ${props.className}`}
      style={props.style}
    >
      {props.iconElm && (!iconPosition || iconPosition === "left") && (
        <props.iconElm
          fill="currentColor"
          htmlColor="currentColor"
          style={{ marginRight: 5 }}
        />
      )}
      {props.children}
      {props.iconElm && iconPosition === "right" && (
        <props.iconElm
          fill="currentColor"
          htmlColor="currentColor"
          style={{ marginLeft: 5 }}
        />
      )}
    </Button>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(PosButton));
