import React from "react";
import NumberFormat, { NumberFormatValues } from "react-number-format";
import { TextField } from "@material-ui/core";
import { ReactElement } from "react";
import clsx from "clsx";
import { AppState } from "../../store";
import { connect, ConnectedProps } from "react-redux";
import { useStyles } from "./PosNumberInput.style";
export interface NumberInputTextFieldProps extends PropsFromRedux {
  onChange: (event: { target: { name?: string; value: string } }) => void;
  name?: string;
  styleInput?: React.CSSProperties;
  max?: number;
  min?: number;
  decimalSeparator?: boolean;
  selectOnFocus?: boolean;
  onKeyDown?: (e: React.KeyboardEvent<HTMLInputElement>) => void;
  value?: number;
  decimalScale?: number;
  id?: string;
  classes?: any;
  inputComponent?:
    | React.ComponentClass<any, any>
    | React.FunctionComponent<any>;
  label?: string | ReactElement;
  disabled?: boolean;
  autoFocus?: boolean;
  onFocus?: () => void;
  style?: React.CSSProperties;
  onBlur?: () => void;
  fontsize?: number | string;
  fontWeight?: number | string;
}

const NumberInputTextField = (props: NumberInputTextFieldProps) => {
  const { onChange, styleInput, max, min, decimalSeparator, ...other } = props;

  const handleFocus = (e: React.FocusEvent<HTMLInputElement>) => {
    if (props.onFocus) {
      props.onFocus();
    }
    if (props.selectOnFocus) {
      e.target.select();
    }
  };

  const handleBlur = (
    e: React.FocusEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    if (props.onBlur) {
      props.onBlur();
    }
  };
  const classes = useStyles(props)();

  return (
    <NumberFormat
      autoFocus={props.autoFocus}
      disabled={props.disabled}
      id={props.id}
      name={props.name}
      label={props.label}
      value={props.value}
      className={clsx(props.classes, classes.defautInput)}
      onChange={(event) => {
        event.stopPropagation();
      }}
      onValueChange={(values: NumberFormatValues) => {
        onChange({ target: { name: props.name, value: values.value } });
      }}
      thousandSeparator
      decimalSeparator={decimalSeparator ? "." : false}
      inputProps={{
        style: {
          ...styleInput,
          textAlign: styleInput?.textAlign ? styleInput.textAlign : "right",
        },
      }}
      style={props.style}
      onFocus={handleFocus}
      onKeyDown={props.onKeyDown}
      isNumericString={true}
      allowNegative={false}
      customInput={props.inputComponent ? props.inputComponent : TextField}
      decimalScale={props.decimalScale}
      autoComplete="off"
      // allowLeadingZeros={false}
      isAllowed={(values) => {
        const { floatValue } = values;
        return floatValue !== undefined
          ? floatValue <= (max ? max : 9999999999) &&
              floatValue >= (min ? min : 0)
          : true;
      }}
      onBlur={handleBlur}
    />
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default connector(NumberInputTextField);
